<!-- 侧边栏 -->
<template>
    <div class="aside">
        <el-col :span="12">
            <el-menu default-active="1" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" background-color="#545c64" text-color="#fff" active-text-color="#ffd04b">
                <el-submenu index="1">
                    <template slot="title">
                        <i class="el-icon-location"></i>
                        <span>哨兵管理</span>
                    </template>
                    <el-menu-item-group>
                        <el-menu-item index="/sentrylist">哨兵列表</el-menu-item>
                        <el-menu-item index="/sentryadd">添加哨兵</el-menu-item>
                    </el-menu-item-group>
                </el-submenu>
                <el-submenu index="2">
                    <template slot="title">
                        <i class="el-icon-location"></i>
                        <span>哨所管理</span>
                    </template>
                    <el-menu-item-group>
                        <el-menu-item index="/postlist">哨所列表</el-menu-item>
                        <el-menu-item index="/postadd">添加哨所</el-menu-item>
                    </el-menu-item-group>
                </el-submenu>
                <el-submenu index="3">
                    <template slot="title">
                        <i class="el-icon-location"></i>
                        <span>积分管理</span>
                    </template>
                    <el-menu-item-group>
                        <el-menu-item index="/Integralrank">积分排行</el-menu-item>
                        <el-menu-item index="/Integralexchange">积分兑换</el-menu-item>
                    </el-menu-item-group>
                </el-submenu>
                <el-submenu index="4">
                    <template slot="title">
                        <i class="el-icon-location"></i>
                        <span>事件管理</span>
                    </template>
                    <el-menu-item-group>
                        <el-menu-item index="/reportevent">事件列表</el-menu-item>
                    </el-menu-item-group>
                </el-submenu>
            </el-menu>
        </el-col>
    </div>
</template>

<script>
export default {
  data() {
    return {}
  },

  components: {},

  computed: {},

  created() {},

  methods: {
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      }
  }
}
</script>
