<template>
  <div class="login">
    <div class="loginBox">
      <!-- model表单数据对象   rules表单验证规则 label-position表单域标签位置  label-width表单域标签的宽度  -->
      <el-form :model="loginForm" :rules="rules2" ref="loginForm" label-position="top" label-width="0px" class="login-container">
        <span class="title">综治哨所后台管理系统</span>
        <el-form-item label="账号" label-width="120px">
          <el-input type="text" v-model="loginForm.account" auto-complete="off" placeholder="请输入账号" @keyup.enter.native="login"></el-input>
        </el-form-item>
        <el-form-item label="密码" label-width="120px">
          <el-input type="password" v-model="loginForm.password" auto-complete="off" placeholder="请输入密码" @keyup.enter.native="login"></el-input>
          <!-- <input type="password"> -->
        </el-form-item>
        <el-form-item>
          <!-- loading是否为加载中状态 @click.native.prevent支持键盘enter @相当于v-on -->
          <!-- @keyup.enter.native="loginSubmit" -->
          <el-button type="primary" class="loginBth" @click.native.prevent="login" :loading="logining">登录</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
// 接口
import { login } from '../axios/api'
// md5加密
import md5 from 'js-md5'

export default {
  data() {
    const validateUsername = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入用户名'))
      } else {
        callback()
      }
    }
    const validatePass = (rule, value, callback) => {
      if (value.length < 6) {
        callback(new Error('密码不能小于6位'))
      } else {
        callback()
      }
    }
    return {
      logining: false,
      loginForm: {
        account: '',
        password: ''
      },
      rules2: {
        account: [
          {
            required: true,
            message: '请输入账号',
            trigger: 'blur'
          }
        ],
        password: [
          {
            required: true,
            message: '请输入密码',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  //这里可以用created或mounted  默认加载
  mounted() {
    console.log("初始账号：admin，初始密码：admin")
  },
  methods: {
    // 用户登录
    login() {
      this.logining = true
      let dataJson = {
        userName: this.loginForm.account,
        password: this.loginForm.password
      }
      dataJson = JSON.stringify(dataJson)
      console.log(dataJson)
      login('/admin/login', dataJson)
        .then(res => {
          console.log(res.data)
          if (res.data.code == '1') {
            let user = res.data.data
            console.log(user)
            sessionStorage.setItem('user', JSON.stringify(user))
            this.$router.push({ path: '/sentrylist' })
          } else {
            this.logining = false
            this.$message({
              message: '账号或密码错误!',
              type: 'warning'
            })
          }
        })
        .catch(err => {
          this.logining = false
          this.$message({
            message: '网络错误!',
            type: 'error'
          })
        })
    }
  }
}
</script>

<style lang='less'>
@import url('../publicResources/common');
.login {
  .outerDiv;
  background-image: linear-gradient(
    -135deg,
    #9ffff2 0%,
    #07cab2 94%,
    #00b59e 100%
  );
  .loginBox {
    .HW100;
    .login-container {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 20rem;
      padding: 2rem 2rem 1rem 2rem;
      background: #fff;
      border: 1px solid #eaeaea;
      box-shadow: 0 0 25px #cac6c6;
      box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.3);
      border-radius: 0.5rem;
      -webkit-border-radius: 0.5rem;
      -moz-border-radius: 0.5rem;
      background-clip: padding-box;
      .title {
        font-family: PingFangSC-Medium;
        font-size: 16px;
        color: #40d1bf;
        text-align: center;
        display: block;
        margin-bottom: 20px;
        letter-spacing: 4px;
        text-indent: 4px;
      }
      .el-input__inner {
        height: 32px;
      }
      .loginBth {
        background: #40d1bf;
        border-radius: 4px;
        width: 100%;
        padding: 8px 20px;
        letter-spacing: 4px;
        text-indent: 4px;
      }
      .el-form-item__label {
        line-height: 40px;
        font-size: 13px;
        float: left;
        padding: 0;
        color: rgba(0, 0, 0, 0.8);
      }
    }
  }
  .el-input {
    width: 85%;
    float: right;
  }
  .el-form--label-top .el-form-item__label {
    width: 15%;
    padding: 0;
  }
}


</style>


