<template>
  <div class="paging">
    <el-row type="flex" justify="center" class="paging">
      <el-col :span="22">
        <div class="block">
          <el-pagination layout="prev, pager, next" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage" :total="tablePageNumber">
          </el-pagination>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 当前页数
      currentPage: 1,
      // 总的数据数
      tablePageNumber: 1000
    }
  },
  // 当前实例创建完成就监听这个事件
  created() {
    this.$root.count.$on('getTablePageNumber', value => {
      this.tablePageNumber = value
    })

    this.$root.count.$on('pageReturnZero', value => {
      this.currentPage = 1
    })
  },
  methods: {
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`) 
      console.log(this.$route.path)
      switch (this.$route.path) {
        case '/sentrylist':
          this.getSentryListTable(val, 10)
          break
        case '/postlist':
          this.getPostListTable(val, 10)
          break
        case '/reporteventlist':
          this.getEventListTable(val, 10)
          break
        case '/Integrallist':
          this.getIntegralListTable(val, 10)
          break
        case '/Integralrank':
          this.getIntegralrankTable(val, 10)
          break
      }
    },
    // 获取哨兵列表数据
    getSentryListTable(page, size) {
      console.log(page + ':' + size)
      // 事件名字自定义，用不同的名字区别事件
      this.$root.page.$emit('getSentryListTable', page)
    },

    // 获取哨所列表数据
    getPostListTable(page, size) {
      console.log(page + ':' + size)
      // 事件名字自定义，用不同的名字区别事件
      this.$root.page.$emit('getPostListTable', page)
    },

    // 获取上报事件列表数据
    getEventListTable(page, size) {
      console.log(page + ':' + size)
      // 事件名字自定义，用不同的名字区别事件
      this.$root.page.$emit('getEventListTable', page)
    },

    // 获取积分列表数据
    getIntegralListTable(page, size) {
      console.log(page + ':' + size)
      // 事件名字自定义，用不同的名字区别事件
      this.$root.page.$emit('getIntegralListTable', page)
    },

    getIntegralrankTable(page, size) {
      console.log(page + ':' + size)
      // 事件名字自定义，用不同的名字区别事件
      this.$root.page.$emit('getIntegralrankTable', page)
    },


    
  }
}
</script>

<style lang="less">
.paging {
  position: fixed;
  bottom: 1rem;
  width: calc(100% - 200px);
  text-align: center;
}
</style>



