<!-- 哨兵模块 -->
<template>
  <div class="sentryList outerDiv" id="sentryList">
    <div class="sentryListBox withinDiv">
      <el-container>
        <el-aside width="200px">
          <el-row class="asideDiv">
            <el-col :span="24" class="aside">
              <span class="logoSpan">
                              <i>综治哨所</i>
                              <i>后台管理系统</i>
                              </span>
              <!-- <img class="logo" src="../assets/logo.jpg" alt="logo"> -->
              <!-- 静态的资源 -->
              <div class="parentNav">
                <div class="nav active">
                  <i class="icon iconfont icon-fl-renshi"></i>
                  <router-link to="/sentrylist">哨兵管理</router-link>
                </div>
                <div class="nav">
                  <i class="icon iconfont icon-suozaidi"></i>
                  <router-link to="/postlist">哨所管理</router-link>
                </div>
                <div class="nav">
                  <i class="icon iconfont icon-paihang"></i>
                  <router-link to="/Integralrank">积分排行</router-link>
                </div>
                <div class="nav">
                  <i class="icon iconfont icon-jifen3"></i>
                  <router-link to="/Integrallist">积分管理</router-link>
                </div>
                <div class="nav">
                  <i class="icon iconfont icon-shijianchaxun-"></i>
                  <router-link to="/reporteventlist">事件管理</router-link>
                </div>
                <div class="nav" v-show="isShowEditUser">
                  <i class="icon iconfont icon-geren9"></i>
                  <router-link to="/userManage">管理中心</router-link>
                </div>
              </div>
            </el-col>
          </el-row>
        </el-aside>
        <el-container>
          <el-header style="height: 40px;">
            <my-header></my-header>
          </el-header>
          <el-main style="border-top: 1rem solid #eee;">
            <div class="sentryListMain">
              <!-- 数据导出进度条 -->
              <el-dialog title="数据导出进度" :visible.sync="tableDataExportModel" width="30%" center>
                <el-progress :percentage="tableDataExportNum" :status="tableDataExportStatus" :color="tableDataExportColor"></el-progress>
              </el-dialog>
              <!-- 添加哨兵 -->
              <el-dialog :title="modelName" custom-class="addOrEditSentry addOrEdit" :visible.sync="addSentryDataModel" width="70%">
                <el-form :model="addOrEditSentryData" :inline="true" class="demo-form-inline" label-position="right" style="width: 100%;">
                  
                  <el-form-item label="哨兵编号" :label-width="formLabelWidth">
                    <el-input size="small" v-model="addOrEditSentryData.sentryNumber" clearable placeholder="请输入哨兵编号(选填)"></el-input>
                  </el-form-item>
                  <el-form-item label="哨兵姓名" :label-width="formLabelWidth">
                    <el-input size="small" v-model="addOrEditSentryData.sentryName" clearable placeholder="请输入姓名"></el-input>
                  </el-form-item>
                  <el-form-item label="性别" :label-width="formLabelWidth">
                    <el-select size="small" v-model="addOrEditSentryData.sentrySex" clearable placeholder="请选择性别">
                      <el-option label="男" value="男"></el-option>
                      <el-option label="女" value="女"></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label="身份证号" :label-width="formLabelWidth">
                    <el-input size="small" v-model="addOrEditSentryData.sentryNumberID" clearable placeholder="请输入身份证号"></el-input>
                  </el-form-item>
                  <el-form-item label="职位" :label-width="formLabelWidth">
                    <el-input size="small" v-model="addOrEditSentryData.sentryPosition" clearable placeholder="请输入职位"></el-input>
                  </el-form-item>

                  <el-form-item v-show="isShowSentryUpload" label="单位\乡镇场" :label-width="formLabelWidth">
                    <el-select size="small" v-model="addOrEditSentryData.postTown" filterable clearable placeholder="请选择单位\乡镇场">
                      <el-option v-for="item in selectTown" :key="item.value" :label="item.label" :value="item.value">
                      </el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item v-show="isShowSentryUpload" label="哨所级别" :label-width="formLabelWidth">
                    <el-select size="small" v-model="addOrEditSentryData.postLevel" clearable placeholder="请选择哨所级别" @change="getPostLevel">
                      <el-option label="一级哨所" value="1"></el-option>
                      <el-option label="二级哨所" value="2"></el-option>
                      <el-option label="三级哨所" value="3"></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item v-show="isShowSentryUpload" label="哨所名称" :label-width="formLabelWidth">
                    <el-select size="small" v-model="addOrEditSentryData.postName" filterable clearable placeholder="请选择哨所名称">
                      <el-option v-for="item in selectPostName" :key="item.value" :label="item.label" :value="item.value">
                      </el-option>
                    </el-select>
                  </el-form-item>

                  <el-form-item v-show="isShowSentryUpload" label="哨所职务" :label-width="formLabelWidth">
                    <el-select size="small" v-model="addOrEditSentryData.postOffice" clearable placeholder="请选择哨所职务">
                      <el-option label="总哨长" value="总哨长"></el-option>
                      <el-option label="副总哨长" value="副总哨长"></el-option>
                      <el-option label="哨长" value="哨长"></el-option>
                      <el-option label="副哨长" value="副哨长"></el-option>
                      <el-option label="哨兵" value="哨兵"></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label="手机号" :label-width="formLabelWidth">
                    <el-input size="small" v-model="addOrEditSentryData.sentryTel" clearable placeholder="请输入手机号"></el-input>
                  </el-form-item>
                  <el-form-item v-show="isShowSentryUpload" label="计分权限" :label-width="formLabelWidth">
                    <el-select size="small" v-model="addOrEditSentryData.isScoring" clearable placeholder="请选择是否有计分权限">
                      <el-option label="是" value="1"></el-option>
                      <el-option label="否" value="0"></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item v-show="isShowSentryUpload" label="处理严重事件" :label-width="formLabelWidth">
                    <el-select size="small" v-model="addOrEditSentryData.isSerious" clearable placeholder="请选择是否可处理严重事件">
                      <el-option label="是" value="1"></el-option>
                      <el-option label="否" value="0"></el-option>
                    </el-select>
                  </el-form-item>
                </el-form>
                  <div class="batchAddSentry" style="display:none;">
                    <el-button size="mini" round>导入表格</el-button>
                    <input id="upload" type="file" value="导入表格" @change="importfxx(this)" accept=".xls,.xlsx,.csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel" />
                  </div>
                <span slot="footer" class="dialog-footer">
                  <el-button size="small" @click="addSentryDataModel = false">取消</el-button>
                  <el-button size="small" type="primary" @click="SubmitSentryData" :loading="loading">提交</el-button>
                </span>
              </el-dialog>

              <!-- 搜索表单 -->
              <el-row class="searchForm">
                <el-form :inline="true" class="demo-form-inline" style="text-align: left;">
                  <el-form-item>
                    <el-select size="small" style="width:180px" v-model="form.unitTown" filterable clearable placeholder="请选择单位\乡镇场" @change="getSelectTown">
                      <el-option v-for="item in selectTown" :key="item.value" :label="item.label" :value="item.value">
                      </el-option>
                    </el-select>
                    <el-select size="small" style="width:140px" v-model="form.postLevel" clearable placeholder="哨所等级" @change="getPostLevel">
                      <el-option label="一级哨所" value="1"></el-option>
                      <el-option label="二级哨所" value="2"></el-option>
                      <el-option label="三级哨所" value="3"></el-option>
                    </el-select>
                    <el-select size="small" style="width:180px" v-model="form.postCode" filterable clearable placeholder="哨所名称" @change="getSentryName">
                      <el-option v-for="item in selectPostName" :key="item.value" :label="item.label" :value="item.value">
                      </el-option>
                    </el-select>
                    <el-input size="small" style="width:180px" placeholder="请输入身份证" clearable v-model="form.sentryNumberID" @change="getSentryData">
                      <i slot="prefix" class="el-input__icon el-icon-search"></i>
                    </el-input>
                    <el-input size="small" style="width:140px" placeholder="请输入姓名" clearable v-model="form.sentryName" @change="getSentryData">
                      <i slot="prefix" class="el-input__icon el-icon-search"></i>
                    </el-input>
                    <el-button size="small" @click="getSentryData" icon="el-icon-search" :loading="loading">查询</el-button>
                    <!-- 导出数据 -->
                    <el-button size="small" @click="export_Excel" icon="el-icon-download" :loading="loading">导出数据</el-button>
                    <el-button size="small" style="position: absolute;right: 0;top: 4px;" @click="addSentryDataModelTrue" icon="el-icon-plus">添加哨兵</el-button>
                  </el-form-item>
                </el-form>
              </el-row>

              <!-- 数据表格 -->
              <el-row>
                <el-table :data="sentryTableData" style="width: 100%" key="id" sentryId="sentryId">
                  <el-table-column type="expand">
                    <template slot-scope="props">
                      <el-form label-position="left" inline class="demo-table-expand">
                        <el-form-item label="姓名">
                          <span>{{ props.row.sentryName }}</span>
                        </el-form-item>
                        <el-form-item label="性别">
                          <span>{{ props.row.sentrySex }}</span>
                        </el-form-item>
                        <el-form-item label="身份证号">
                          <span>{{ props.row.sentryNumberID }}</span>
                        </el-form-item>
                        <el-form-item label="职位">
                          <span>{{ props.row.postOffice }}</span>
                        </el-form-item>
                        <el-form-item label="哨所级别">
                          <span>{{ props.row.postLevel }}</span>
                        </el-form-item>
                        <el-form-item label="哨所名称">
                          <span>{{ props.row.postName }}</span>
                        </el-form-item>
                        <el-form-item label="哨所编号">
                          <span>{{ props.row.postNumber }}</span>
                        </el-form-item>
                        <el-form-item label="哨所职务">
                          <span>{{ props.row.sentryPosition }}</span>
                        </el-form-item>
                        <el-form-item label="手机号">
                          <span>{{ props.row.sentryTel }}</span>
                        </el-form-item>
                      </el-form>
                    </template>
                  </el-table-column>
                  <el-table-column label="哨兵编号" prop="index" width="120">
                  </el-table-column>
                  <el-table-column label="姓名" prop="sentryName" width="100">
                  </el-table-column>
                  <el-table-column label="身份证号" prop="sentryNumberID" width="200">
                  </el-table-column>
                  <el-table-column label="职位" prop="sentryPosition" width="160">
                  </el-table-column>
                  <el-table-column label="哨所名称" prop="postName">
                  </el-table-column>
                  <el-table-column label="哨所职务" prop="postOffice" width="120">
                  </el-table-column>

                  <el-table-column fixed="right" label="操作" width="140">
                    <template slot-scope="scope">
                      <i class="el-icon-edit" @click.stop="editSentryInfo(scope.$index, scope.row)"></i>
                      <i class="el-icon-delete" @click.stop="deleteSentryInfo(scope.$index, scope.row)"></i>
                    </template>
                  </el-table-column>
                </el-table>
              </el-row>
            </div>
            <my-paging></my-paging>
          </el-main>
        </el-container>
      </el-container>
    </div>
  </div>
</template>

<script>
import myAside from "../components/aside.vue";
import myPaging from "../components/paging.vue";
import myHeader from "../components/header.vue";
import {
  getTownList,
  getAllPostList,
  getSentryList,
  delectSentry,
  uploadSentry,
  addSentry,
  getPostList
} from "../axios/api";

export default {
  data() {
    return {
      isBatchImportSuccess: true,
      // 点击按钮加载状态
      loading: false,
      //添加、编辑哨兵模态框
      addSentryDataModel: false,

      // 表格数据导出模态框
      tableDataExportModel: false,
      tableDataExportNum: 0,
      tableDataExportColor: "#8e71c7",
      tableDataExportStatus: "",
      tableDataExportPostName: "",
      // 导出表格数据
      exportTableData: [],
      // 判断是否导出完毕
      isExportDataComplete: false,

      formLabelWidth: "120",
      // 添加、编辑哨兵数据
      addOrEditSentryData: {
        sentryNumber: "",
        sentryName: "",
        sentrySex: "",
        sentryNumberID: "",
        sentryPosition: "",
        postLevel: "",
        postName: "",
        postNumber: "",
        postOffice: "",
        sentryTel: "",
        isScoring: "",
        isSerious: "",
        postTown: ""
      },
      editSentryId: 0,
      editSentrysentryId: 0,
      sentryCode: "",
      PostCode: "",
      modelName: "添加哨兵",
      isShowSentryUpload: true,
      pageNum: 0, //当前页数
      pageStripNum: 10, //每页的条数
      // 搜索表单
      form: {
        unitTown: "",
        postLevel: "",
        postCode: "",
        sentryNumberID: "",
        sentryName: ""
      },
      isShowEditUser: false,
      // 乡镇场、单位名称
      selectTown: [],
      // 哨所名称
      selectPostName: [],
      // 表格数据
      sentryTableData: [],

      // 数据总条数
      tablePageNumber: 0,

      // reload : true,
      isAddType: true //判断是否处于新增模式,
    };
  },

  components: {
    myAside,
    myPaging,
    myHeader
  },

  created() {
    // 获取乡镇县直单位
    this.getTownList();
    // 获取表格数据
    this.getSentryData(this.form);

    // 深拷贝了一份数据模板,用于清空表单数据
    this.addOrEditSentryDataEmpty = JSON.parse(
      JSON.stringify(this.addOrEditSentryData)
    );

    let user = JSON.parse(sessionStorage.getItem("user"));
    if (user.grade == "1") this.isShowEditUser = true;
  },

  mounted() {
    //   控制翻页功能(非父子组件之间的通信)
    this.$root.page.$on("getSentryListTable", value => {
      this.pageNum = value - 1;
      console.log(this.pageNum);
      this.getSentryData(this.form);
    });
  },
  //离开当前页面后执行
  destroyed: function() {
    console.log("离开当前页面后执行");
    // 清空搜索条件对象
    for (let key in this.form) {
      this.form[key] = "";
    }
  },

  methods: {
    // 导入表格
    importfxx(obj) {
      let _this = this;
      let inputDOM = this.$refs.inputer;
      // 通过DOM取文件数据
      this.file = event.currentTarget.files[0];
      var rABS = false; //是否将文件读取为二进制字符串
      var f = this.file;
      var reader = new FileReader();
      //if (!FileReader.prototype.readAsBinaryString) {
      FileReader.prototype.readAsBinaryString = function(f) {
        var binary = "";
        var rABS = false; //是否将文件读取为二进制字符串
        var pt = this;
        var wb; //读取完成的数据
        var outdata;
        var reader = new FileReader();
        reader.onload = function(e) {
          var bytes = new Uint8Array(reader.result);
          var length = bytes.byteLength;
          for (var i = 0; i < length; i++) {
            binary += String.fromCharCode(bytes[i]);
          }
          var XLSX = require("xlsx");
          if (rABS) {
            wb = XLSX.read(btoa(fixdata(binary)), {
              //手动转化
              type: "base64"
            });
          } else {
            wb = XLSX.read(binary, {
              type: "binary"
            });
          }
          outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]); //outdata就是你想要的东西
          console.log("outdata就是你想要的东西");
          console.log(outdata);
          _this.batchAddSentry(outdata);
        };
        reader.readAsArrayBuffer(f);
      };
      if (rABS) {
        reader.readAsArrayBuffer(f);
      } else {
        reader.readAsBinaryString(f);
      }
    },

    // 批量添加哨兵
    batchAddSentry(batchData) {
      console.log("批量添加哨兵");
      for (let i in batchData) {
        let data = {
          idNumber: batchData[i].身份证号,
          postCode: "",
          postOffice: batchData[i].哨所职务,
          canScoring: batchData[i].是否拥有计分权限 == "是" ? "1" : "0",
          isPolice: batchData[i].是否驻村民警 == "是" ? "1" : "0",
          phone: batchData[i].手机号,
          position: batchData[i].职位,
          postGrade: batchData[i].哨所级别,
          name: batchData[i].哨兵姓名,
          sex: batchData[i].性别
        };
        console.log("add");
        setTimeout(
          this.getPostCodeByPostName(
            this.batchAddSentryFun,
            batchData[i].哨所名称,
            data
          ),
          1000
        );
      }
    },

    batchAddSentryFun(data) {
      console.log(data);
      addSentry("/sentry/add", data)
        .then(res => {
          console.log(res);
          if (res.data.code == "1") {
            console.log("OK");
            // this.$message({
            //   type: 'success',
            //   message: '添加哨兵信息成功!'
            // })
            // // 重新更新病例列表数据
            // this.getSentryData(this.form)
          } else if (res.data.code == "0") {
            this.$message({
              message: res.data.data,
              type: "warning"
            });
          } else if (res.data.code == "-1") {
            this.$message({
              message: "网络错误!",
              type: "error"
            });
          }
        })
        .catch(err => {
          console.log(err);
          console.log("添加失败");
          console.log(data.name);
          this.batchAddSentryFun(data);
          this.$message({
            message: "网络错误!",
            type: "error"
          });
        });
    },

    // 通过哨所名称拿到哨所编号http://192.168.1.67:8889/post/page?page=0&size=10&code=&grade=&name=%E4%B8%87%E5%9C%BA%E9%9B%81%E9%83%BD%E7%A4%BE%E5%8C%BA%E7%BB%BC%E6%B2%BB%E5%93%A8%E6%89%80&number=
    getPostCodeByPostName(callback, postName, data) {
      getPostList(
        "/post/page?page=0&size=10&code=&grade=&name=" + postName + "&number="
      )
        .then(res => {
          console.log(res);
          if (res.data.code == "1") {
            let postCode = res.data.data.content[0].code;
            console.log(postCode);
            data.postCode = postCode;
            this.batchAddSentryPost = postCode;
            callback(data);
          } else if (res.data.code == "0") {
            this.$message({
              message: res.data.data,
              type: "warning"
            });
          } else if (res.data.code == "-1") {
            this.$message({
              message: "网络错误!",
              type: "error"
            });
          }
        })
        .catch(err => {
          console.log(err);
          this.$message({
            message: "网络错误",
            type: "error"
          });
        });
    },

    // 导出表格数据
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]));
    },
    export_Excel() {
      this.$confirm("确定要导出表格数据么？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          let postCode = this.form.unitTown;
          this.getPostNameByPostCode(postCode);
          this.exportTableDataFun();
          this.tableDataExportModel = true;
          console.log(this.isExportDataComplete);
        })
        .catch(() => {});
    },

    // 导出函数
    exportDataTrue() {
      require.ensure([], () => {
        const { exportJsonToExcel } = require("../vendor/Export2Excel");
        const tHeader = [
          "哨兵编号",
          "姓名",
          "性别",
          "身份证号",
          "职位",
          "哨所级别",
          "哨所名称",
          "哨所编号",
          "哨所职务",
          "手机号",
          "是否拥有计分权限",
          "是否驻村民警"
        ];
        const filterVal = [
          "index",
          "sentryName",
          "sentrySex",
          "sentryNumberID",
          "position",
          "postLevel",
          "postName",
          "postNumber",
          "postOffice",
          "sentryTel",
          "isScoring",
          "isSerious"
        ];
        const list = this.exportTableData;
        const data = this.formatJson(filterVal, list);
        exportJsonToExcel(
          tHeader,
          data,
          this.tableDataExportPostName + "哨兵列表"
        );
      });
    },

    // 根据哨所编号查找哨所名称
    getPostNameByPostCode(postCode) {
      if (postCode == "") {
        this.tableDataExportPostName = "全部";
      } else {
        console.log(this.selectTown);
        for (let i in this.selectTown) {
          if (this.selectTown[i].value == postCode) {
            this.tableDataExportPostName = this.selectTown[i].label;
          }
        }
      }
    },

    // 导出表格数据http://116.62.138.218:8889/sentry/page?page=0&size=10000&idNumber=&name=&postCode=1-
    exportTableDataFun() {
      let page = 0;
      let size = 10000;
      let idNumber = "";
      let name = "";
      let postCode = "";
      if (this.form.unitTown == "") {
        postCode = "";
      } else {
        postCode = this.form.unitTown + "-";
      }
      console.log(postCode);
      console.log("导出表格数据");
      const result = [];
      getSentryList(
        "/sentry/page?page=" +
          page +
          "&size=" +
          size +
          "&idNumber=" +
          idNumber +
          "&name=" +
          name +
          "&postCode=" +
          postCode
      )
        .then(res => {
          console.log(res);
          if (res.data.code == "1") {
            let resData = res.data.data.content;
            console.log(res.data.data);
            for (let i = 0; i < resData.length; i++) {
              console.log(parseInt(100 * (i / resData.length)));
              this.tableDataExportNum = parseInt(100 * (i / resData.length));
              result.push({
                index: (Array(5).join(0) + resData[i].allSerial).slice(-5),
                sentryName: resData[i].name,
                sentrySex: resData[i].sex || "未知",
                sentryNumberID: resData[i].idNumber || "未知",
                position: resData[i].position || "未知",
                postLevel: resData[i].postGrade || "未知",
                postName: resData[i].post.name || "未知",
                postNumber: resData[i].post.number || "未知",
                postOffice: resData[i].postOffice || "未知",
                sentryTel: resData[i].phone || "未知",
                isScoring: resData[i].canScoring || "未知",
                isSerious: resData[i].isPolice || "未知"
              });
            }
            this.tableDataExportNum = 100;
            this.tableDataExportColor = "";
            this.tableDataExportStatus = "success";
            setTimeout((this.tableDataExportModel = false), 1000);
            console.log("下载完成");
            console.log(result);
            this.exportTableData = result;
            this.isExportDataComplete = true;
            this.exportDataTrue();
            console.log(this.isExportDataComplete);
          } else if (res.data.code == "0") {
            this.tableDataExportStatus = "exception";
            this.$message({
              message: res.data.data,
              type: "warning"
            });
          } else if (res.data.code == "-1") {
            this.tableDataExportStatus = "exception";
            this.$message({
              message: "网络错误!",
              type: "error"
            });
          }
        })
        .catch(err => {
          console.log(err);
          this.tableDataExportStatus = "exception";
          this.$message({
            message: "网络错误",
            type: "error"
          });
        });
    },

    addSentryDataModelTrue() {
      this.addSentryDataModel = true;
      this.modelName = "添加哨兵";
      this.isShowSentryUpload = true;
      this.isAddType = true;
      this.addOrEditSentryData = JSON.parse(
        JSON.stringify(this.addOrEditSentryDataEmpty)
      );
    },
    indexMethod(index) {
      return index * 1;
    },

    // 清空选择条件
    getSelectTown() {
      this.form.postLevel = "";
      this.form.postCode = "";
      this.form.sentryNumberID = "";
      this.form.sentryName = "";
      this.currentPage = 1;
      this.$root.count.$emit("pageReturnZero", this.currentPage);
      this.pageNum = 0;
      console.log(this.form.unitTown);
      this.getSentryData(this.form);
    },

    // getTownList获取乡镇县直单位
    getTownList() {
      const result = [];
      console.log("获取乡镇县直单位");
      getTownList("/unit/all")
        .then(res => {
          if (res.data.code == "1") {
            let resData = res.data.data;
            for (let i = 0; i < resData.length; i++) {
              result.push({
                value: resData[i].code,
                label: resData[i].name
              });
            }
            this.selectTown = result;
          } else if (res.data.code == "0") {
            this.$message({
              message: res.data.data,
              type: "warning"
            });
          } else if (res.data.code == "-1") {
            this.$message({
              message: "网络错误!",
              type: "error"
            });
          }
        })
        .catch(err => {
          console.log(err);
          this.$message({
            message: "网络错误",
            type: "error"
          });
        });
    },

    // 获取哨所等级
    getPostLevel() {
      this.selectPostName = "";
      this.form.postCode = "";
      this.form.sentryNumberID = "";
      this.form.sentryName = "";
      this.currentPage = 1;
      this.$root.count.$emit("pageReturnZero", this.currentPage);
      this.pageNum = 0;
      this.getAllPostListFun();
      this.getSentryData(this.form);
    },

    getSentryName() {
      this.currentPage = 1;
      this.$root.count.$emit("pageReturnZero", this.currentPage);
      this.pageNum = 0;
      this.getSentryData(this.form);
    },

    // 获取哨所列表getAllPostList
    getAllPostListFun() {
      const result = [];
      let addOrEditSentryData = this.addOrEditSentryData;
      let unitTown = this.form.unitTown || addOrEditSentryData.postTown;
      let postLevel = this.form.postLevel || addOrEditSentryData.postLevel;
      console.log("获取哨所列表");
      getAllPostList("/post/all?code=" + unitTown + "-&grade=" + postLevel)
        .then(res => {
          if (res.data.code == "1") {
            let resData = res.data.data;
            console.log(resData)
            for (let i = 0; i < resData.length; i++) {
              result.push({
                value: resData[i].code,
                label: resData[i].name
              });
            }
            this.selectPostName = result;
            
          } else if (res.data.code == "0") {
            this.$message({
              message: res.data.data,
              type: "warning"
            });
          } else if (res.data.code == "-1") {
            this.$message({
              message: "网络错误!",
              type: "error"
            });
          }
        })
        .catch(err => {
          console.log(err);
          this.$message({
            message: "网络错误",
            type: "error"
          });
        });
    },

    // 获取哨兵表格数据
    getSentryData(formData) {
      console.log("搜索关键字：" + JSON.stringify(this.form));
      console.log(this.form.unitTown);
      console.log(this.form.postLevel);
      console.log(this.form.postCode);
      // let formData = this.form
      let postCode = "";
      if (
        formData.unitTown == "" &&
        formData.postLevel == "" &&
        formData.postCode == ""
      ) {
        postCode = "";
      } else if (
        formData.unitTown != "" &&
        formData.postLevel == "" &&
        formData.postCode == ""
      ) {
        postCode = formData.unitTown + "-";
      } else if (
        formData.unitTown != "" &&
        formData.postLevel != "" &&
        formData.postCode == ""
      ) {
        postCode = formData.unitTown + "-" + formData.postLevel;
      } else if (
        formData.unitTown != "" &&
        formData.postLevel != "" &&
        formData.postCode != ""
      ) {
        postCode = formData.postCode;
      }
      if (postCode == undefined) postCode = "";
      let name = this.form.sentryName || "";
      let idNumber = this.form.sentryNumberID || "";
      let page = this.pageNum;
      let size = this.pageStripNum;
      const result = [];
      getSentryList(
        "/sentry/page?page=" +
          page +
          "&size=" +
          size +
          "&idNumber=" +
          idNumber +
          "&name=" +
          name +
          "&postCode=" +
          postCode
      )
        .then(res => {
          console.log(res);
          if (res.data.code == "1") {
            let resData = res.data.data.content;
            console.log(res.data.data);
            this.tablePageNumber = res.data.data.totalElements;
            this.$root.count.$emit("getTablePageNumber", this.tablePageNumber);
            for (let i = 0; i < resData.length; i++) {
              result.push({
                sentryName: resData[i].name,
                sentrySex: resData[i].sex || "未知",
                sentryNumberID: resData[i].idNumber || "未知",
                sentryPosition: resData[i].position || "未知",
                postLevel: resData[i].postGrade || "未知",
                postName: resData[i].post.name || "未知",
                postNumber: resData[i].post.number || "未知",
                postOffice: resData[i].postOffice || "未知",
                sentryTel: resData[i].phone || "未知",
                isScoring: resData[i].canScoring || "未知",
                isSerious: resData[i].isPolice || "未知",
                id: resData[i].id,
                sentryId: resData[i].sentry.id,
                sentryCode: resData[i].sentryCode,
                postCode: resData[i].postCode,
                index: (Array(5).join(0) + resData[i].allSerial).slice(-5)
              });
            }
            console.log(result);
            this.sentryTableData = result;
          } else if (res.data.code == "0") {
            this.$message({
              message: res.data.data,
              type: "warning"
            });
          } else if (res.data.code == "-1") {
            this.$message({
              message: "网络错误!",
              type: "error"
            });
          }
        })
        .catch(err => {
          console.log(err);
          this.$message({
            message: "网络错误",
            type: "error"
          });
        });
    },

    // 编辑哨兵
    editSentryInfo(index, row) {
      console.log(index, row);
      this.modelName = "编辑哨兵";
      this.isShowSentryUpload = false;
      this.addSentryDataModel = true;
      this.isAddType = false;
      this.editSentryId = row.id;
      this.editSentrysentryId = row.sentryId;
      this.sentryCode = row.sentryCode;
      this.postCode = row.postCode;
      this.addOrEditSentryData.sentryNumber = row.index;
      this.addOrEditSentryData.sentryName = row.sentryName;
      this.addOrEditSentryData.sentrySex = row.sentrySex;
      this.addOrEditSentryData.sentryNumberID = row.sentryNumberID;
      this.addOrEditSentryData.sentryPosition = row.sentryPosition;
      this.addOrEditSentryData.postLevel = row.postLevel;
      this.addOrEditSentryData.postName = row.postName;
      this.addOrEditSentryData.postNumber = row.postNumber;
      this.addOrEditSentryData.postOffice = row.postOffice;
      this.addOrEditSentryData.sentryTel = row.sentryTel;
      this.addOrEditSentryData.isScoring = row.isScoring;
      this.addOrEditSentryData.isSerious = row.isSerious;
    },

    // 提交---哨兵的编辑和提交
    SubmitSentryData() {
      if (this.isAddType) {
        let data = {
          idNumber: this.addOrEditSentryData.sentryNumberID,
          postCode: this.addOrEditSentryData.postName,
          postOffice: this.addOrEditSentryData.postOffice,
          canScoring: this.addOrEditSentryData.isScoring,
          isPolice: this.addOrEditSentryData.isSerious,
          phone: this.addOrEditSentryData.sentryTel,
          position: this.addOrEditSentryData.sentryPosition,
          postGrade: this.addOrEditSentryData.postLevel,
          name: this.addOrEditSentryData.sentryName,
          sex: this.addOrEditSentryData.sentrySex
        };
        console.log(data);
        console.log("add");
        addSentry("/sentry/add", data)
          .then(res => {
            console.log(res);
            if (res.data.code == "1") {
              this.$message({
                type: "success",
                message: "添加哨兵信息成功!"
              });
              this.addOrEditSentryData = JSON.parse(
                JSON.stringify(this.addOrEditSentryDataEmpty)
              );
              this.addSentryDataModel = false;
              this.isShowSentryUpload = true;
              // 重新更新病例列表数据
              this.getSentryData(this.form);
            } else if (res.data.code == "0") {
              this.$message({
                message: res.data.data,
                type: "warning"
              });
            } else if (res.data.code == "-1") {
              this.$message({
                message: "网络错误!",
                type: "error"
              });
            }
          })
          .catch(err => {
            this.$message({
              message: "网络错误!",
              type: "error"
            });
          });
      } else {
        let data = {
          id: this.editSentryId,
          allSerial: (Array(5).join(0) + this.addOrEditSentryData.sentryNumber).slice(-5),
          idNumber: this.addOrEditSentryData.sentryNumberID,
          postOffice: this.addOrEditSentryData.postOffice,
          canScoring: this.addOrEditSentryData.isScoring,
          isPolice: this.addOrEditSentryData.isSerious,
          phone: this.addOrEditSentryData.sentryTel,
          position: this.addOrEditSentryData.sentryPosition,
          name: this.addOrEditSentryData.sentryName,
          sex: this.addOrEditSentryData.sentrySex,
          sentryCode: this.sentryCode,
          postCode: this.postCode,
          sentry: {
            id: this.editSentrysentryId,
            idNumber: this.addOrEditSentryData.sentryNumberID,
            name: this.addOrEditSentryData.sentryName,
            sex: this.addOrEditSentryData.sentrySex
          }
        };
        console.log(data);
        console.log("upload");
        uploadSentry("/sentry/update", data)
          .then(res => {
            console.log(res);
            if (res.data.code == "1") {
              this.$message({
                type: "success",
                message: "修改哨兵信息成功!"
              });
              this.addOrEditSentryData = JSON.parse(
                JSON.stringify(this.addOrEditSentryDataEmpty)
              );
              this.addSentryDataModel = false;
              this.isShowSentryUpload = true;
              // 重新更新病例列表数据
              this.getSentryData(this.form);
            } else if (res.data.code == "0") {
              this.$message({
                message: res.data.data,
                type: "warning"
              });
            } else if (res.data.code == "-1") {
              this.$message({
                message: "网络错误!",
                type: "error"
              });
            }
          })
          .catch(err => {
            this.$message({
              message: "网络错误!",
              type: "error"
            });
          });
      }
    },

    // 删除哨兵提示框
    deleteSentryInfo(index, row) {
      console.log(index, row);
      let delectId = row.id;
      console.log("delectId:" + delectId);
      this.$confirm("此操作将永久删除该哨兵, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.deleteSentryInfoSure(delectId);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },

    // 删除哨兵数据
    deleteSentryInfoSure(delectId) {
      console.log("delete");
      console.log(delectId);
      delectSentry("/sentry/del?id=" + delectId)
        .then(res => {
          console.log(res);
          if (res.data.code == "1") {
            this.$message({
              type: "success",
              message: "删除成功!"
            });
            // 重新更新病例列表数据
            this.getSentryData(this.form);
          } else if (res.data.code == "0") {
            this.$message({
              message: res.data.data,
              type: "warning"
            });
          } else if (res.data.code == "-1") {
            this.$message({
              message: "网络错误!",
              type: "error"
            });
          }
        })
        .catch(err => {
          this.$message({
            message: "网络错误!",
            type: "error"
          });
        });
    }
  }
};
</script>


<style lang='less'>
@import "../publicResources/common";

.sentryList {
  .sentryListBox {
    .el-container {
      table.el-table__body {
        .el-form-item__content {
          width: auto;
        }
        .demo-table-expand .el-form-item {
          width: 30%;
        }
        .demo-table-expand label {
          width: 120px;
        }
      }
    }
  }
}
</style>


