<!-- 积分模块 -->
<template>
  <div class="IntegralList outerDiv" id="IntegralList">
    <div class="IntegralListBox withinDiv">
      <el-container>
        <el-aside width="200px">
          <el-row class="asideDiv">
            <el-col :span="24" class="aside">
              <span class="logoSpan">
                              <i>综治哨所</i>
                              <i>后台管理系统</i>
                              </span>
              <!-- <img class="logo" src="../assets/logo.jpg" alt="logo"> -->
              <!-- 静态的资源 -->
              <div class="parentNav">
                <div class="nav">
                  <i class="icon iconfont icon-fl-renshi"></i>
                  <router-link to="/sentrylist">哨兵管理</router-link>
                </div>
                <div class="nav">
                  <i class="icon iconfont icon-suozaidi"></i>
                  <router-link to="/postlist">哨所管理</router-link>
                </div>
                <div class="nav">
                  <i class="icon iconfont icon-paihang"></i>
                  <router-link to="/Integralrank">积分排行</router-link>
                </div>
                <div class="nav active">
                  <i class="icon iconfont icon-jifen3"></i>
                  <router-link to="/Integrallist">积分管理</router-link>
                </div>
                <div class="nav">
                  <i class="icon iconfont icon-shijianchaxun-"></i>
                  <router-link to="/reporteventlist">事件管理</router-link>
                </div>
                <div class="nav" v-show="isShowEditUser">
                  <i class="icon iconfont icon-geren9"></i>
                  <router-link to="/userManage">管理中心</router-link>
                </div>
              </div>
            </el-col>
          </el-row>
        </el-aside>
        <el-container>
          <el-header style="height: 40px;">
            <my-header></my-header>
          </el-header>
          <el-main style="border-top: 1rem solid #eee;">
            <div class="sentryListMain">
               <!-- 数据导出进度条 -->
              <el-dialog
                title="数据导出进度"
                :visible.sync="tableDataExportModel"
                width="30%"
                center>
                <el-progress :percentage="tableDataExportNum" :status="tableDataExportStatus" :color="tableDataExportColor" ></el-progress>
              </el-dialog>
              <!-- 搜索表单 -->
              <el-row class="searchForm">
                <el-form :inline="true" class="demo-form-inline" style="text-align: left;">
                  <el-form-item>
                    <el-select size="small" style="width:180px" v-model="form.unitTown" filterable clearable placeholder="请选择单位\乡镇场" @change="getSelectTown">
                      <el-option v-for="item in selectTown" :key="item.value" :label="item.label" :value="item.value">
                      </el-option>
                    </el-select>
                    <el-select size="small" style="width:140px" v-model="form.postLevel" filterable clearable placeholder="哨所等级" @change="getPostLevel">
                      <el-option label="一级哨所" value="1"></el-option>
                      <el-option label="二级哨所" value="2"></el-option>
                      <el-option label="三级哨所" value="3"></el-option>
                    </el-select>
                    <el-select size="small" style="width:180px" v-model="form.postCode" filterable clearable placeholder="哨所名称" @change="getPostName">
                      <el-option v-for="item in selectPostName" :key="item.value" :label="item.label" :value="item.value">
                      </el-option>
                    </el-select>
                    <el-input size="small" style="width:140px" placeholder="上报哨兵姓名" clearable v-model="form.sentryName" @change="getTableData">
                      <i slot="prefix" class="el-input__icon el-icon-search"></i>
                    </el-input>
                    <el-input size="small" style="width:140px" placeholder="审批人姓名" clearable v-model="form.passSentryName" @change="getTableData">
                      <i slot="prefix" class="el-input__icon el-icon-search"></i>
                    </el-input>
                    <el-select size="small" style="width:140px" v-model="form.way" filterable clearable placeholder="积分类型" @change="getPostLevel">
                      <el-option label="上报事件" value="0"></el-option>
                      <el-option label="积分兑换" value="1"></el-option>
                      <el-option label="哨长扣分" value="2"></el-option>
                    </el-select>
                    <el-select size="small" style="width:100px;display:none;" v-model="form.eventState" filterable clearable placeholder="审批状态" @change="getIntegralState">
                      <el-option label="审批中" value="0"></el-option>
                      <el-option label="已完成" value="1"></el-option>
                      <el-option label="被拒绝" value="2"></el-option>
                    </el-select>
                    <el-date-picker size="small" style="width:140px" v-model="form.reportStartTime" @change="unlockReportEndTime" value-format="yyyy-MM-dd" align="right" type="date" placeholder="开始时间" :picker-options="pickerOptions">
                    </el-date-picker>
                    <el-date-picker size="small" style="width:140px" :disabled="isReportStartTime" v-model="form.reportEndTime" value-format="yyyy-MM-dd" align="right" type="date" placeholder="结束时间" :picker-options="pickerOptions">
                    </el-date-picker>
                    <el-button size="small" @click="getTableData" icon="el-icon-search" :loading="loading">查询</el-button>
                    <!-- 导出数据 -->
                    <el-button size="small" @click="export_Excel" icon="el-icon-download" :loading="loading">导出数据</el-button>
                  </el-form-item>
                </el-form>
              </el-row>

              <!-- 数据表格 -->
              <el-row>
                <el-table :data="exchangeTableData" style="width: 100%">
                  <!-- <el-table-column type="expand">
                    <template slot-scope="props">
                      <el-form label-position="left" inline class="demo-table-expand">
                        <el-form-item label="兑换结果">
                          <span>{{ props.row.exchangeResult }}</span>
                        </el-form-item>
                        <el-form-item label="申请人">
                          <span>{{ props.row.applyName }}</span>
                        </el-form-item>
                        <el-form-item label="申请时间">
                          <span>{{ props.row.applyTime }}</span>
                        </el-form-item>
                        <el-form-item label="兑换积分数">
                          <span>{{ props.row.exchangeNum }}</span>
                        </el-form-item>
                        <el-form-item label="处理人">
                          <span>{{ props.row.handleName }}</span>
                        </el-form-item>
                        <el-form-item label="商铺名">
                          <span>{{ props.row.shopName }}</span>
                        </el-form-item>
                        <el-form-item label="物件名">
                          <span>{{ props.row.goodsName }}</span>
                        </el-form-item>
                        <el-form-item label="地点">
                          <span>{{ props.row.shopAddress }}</span>
                        </el-form-item>
                        <el-form-item label="店主姓名">
                          <span>{{ props.row.shopBossName }}</span>
                        </el-form-item>
                        <el-form-item label="店主联系方式">
                          <span>{{ props.row.shopBossPhone }}</span>
                        </el-form-item>
                      </el-form>
                    </template>
                  </el-table-column> -->
                  <el-table-column label="序号" type="index" :index="indexMethod">
                  </el-table-column>
                  <el-table-column label="积分类型" prop="way">
                  </el-table-column>
                  <el-table-column label="申请人" prop="applyName">
                  </el-table-column>
                  <el-table-column label="申请时间" prop="applyTime">
                  </el-table-column>
                  <el-table-column label="积分数" prop="exchangeNum">
                  </el-table-column>
                  <el-table-column label="处理人" prop="passSentryName">
                  </el-table-column>
                  <el-table-column fixed="right" label="操作">
                    <template slot-scope="scope">
                      <i class="el-icon-delete" @click.stop="deleteIntegralApply(scope.$index, scope.row)"></i>
                    </template>
                  </el-table-column>
                </el-table>
              </el-row>
            </div>
            <my-paging></my-paging>
          </el-main>
        </el-container>
      </el-container>
    </div>
  </div>
</template>

<script>
import myAside from '../components/aside.vue'
import myPaging from '../components/paging.vue'
import myHeader from '../components/header.vue'
import {
  getTownList,
  getAllPostList,
  getIntegralApplyList,
  delectIntegralApply
} from '../axios/api'
export default {
  data() {
    return {
      // 表格数据导出模态框
      tableDataExportModel: false,
      tableDataExportNum: 0,
      tableDataExportColor: '#8e71c7',
      tableDataExportStatus:"",
      formLabelWidth: '120',
      tableDataExportPostName: '',
      // 导出表格数据
      exportTableData: [],
      // 判断是否导出完毕
      isExportDataComplete: false,
      YearMonthDay: '', //当前年月日
      isShowEditUser:true,
      // 点击按钮加载状态
      loading: false,
      // 搜索表单
      form: {
        unitTown: '',
        postLevel: '',
        postCode: '',
        sentryName: '',
        startTime: '',
        endTime: '',
        eventState: '1',
        reportTime: '',
        passSentryName : '',
        way: '',
        reportStartTime: '',
        reportEndTime: ''
      },
      pageNum: 0, //当前页数
      pageStripNum: 10, //每页的条数
      // 乡镇场、单位名称
      selectTown: [],
      // 哨所名称
      selectPostName: [],
      //哨兵姓名
      // selectSentryName: [],
      // 表格数据
      exchangeTableData: [],
      // 申请时间
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date())
            }
          },
          {
            text: '昨天',
            onClick(picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24)
              picker.$emit('pick', date)
            }
          },
          {
            text: '一周前',
            onClick(picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', date)
            }
          }
        ]
      },
      isReportStartTime: true,   //是否选择了开始时间
    }
  },

  components: {
    myAside,
    myPaging,
    myHeader
  },

  computed: {},

  created() {
    let user = JSON.parse(sessionStorage.getItem('user'));
    if(user.grade == '1') this.isShowEditUser=true;
    // 获取乡镇县直单位
    this.getTownList()
    // 获取表格数据
    this.getTableData()
    //   控制翻页功能(非父子组件之间的通信)
    this.$root.page.$on('getIntegralListTable', value => {
      this.pageNum = value - 1
      console.log(this.pageNum)
      this.getTableData()
    })
  },
  //离开当前页面后执行
  destroyed: function () {
    console.log("离开当前页面后执行")
      // 清空搜索条件对象
    for(let key in this.form){
      this.form[key] = ""
    }
  },

  methods: {
    // 解锁结束时间
    unlockReportEndTime(){
      if(this.form.reportStartTime != null){
        this.isReportStartTime = false
      }
    },
    // 导出表格数据
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]))
    },
    export_Excel() {
      this.$confirm('确定要导出表格数据么？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          let postCode = this.form.unitTown
          console.log(postCode)
          this.getPostNameByPostCode(postCode)
          this.exportTableDataFun()
          this.tableDataExportModel = true
          console.log(this.isExportDataComplete)
        })
        .catch((err) => {
          console.log(err)
        })
    },

    // 导出函数
    exportDataTrue() {
      require.ensure([], () => {
        const { exportJsonToExcel } = require('../vendor/Export2Excel')
        const tHeader = [
          '积分类型',
          '申请人',
          '申请时间',
          '积分数',
          '处理人',
        ]
        const filterVal = [
          'way',
          'applyName',
          'applyTime',
          'exchangeNum',
          'passSentryName',
        ]
        const list = this.exportTableData
        const data = this.formatJson(filterVal, list)
        exportJsonToExcel(tHeader, data, this.tableDataExportPostName+'积分列表')
      })
    },

    getYearMonthDay(){

    },

    // 根据哨所编号查找哨所名称
    getPostNameByPostCode(postCode){
      if(postCode == ''){
        this.tableDataExportPostName = "全部"
      }else{
        console.log(this.selectTown)
        for(let i in this.selectTown){
          if(this.selectTown[i].value == parseInt(postCode)){
            this.tableDataExportPostName = this.selectTown[i].label
          }
        }
      }
    },

    // 导出表格数据http://116.62.138.218:8889/sentry/page?page=0&size=10000&idNumber=&name=&postCode=1-
    exportTableDataFun() {
      let page = 0
      let size = 10000
      let grade = ''
      let code = ''
      let name = ''
      let isPass = ''
      let time = ''
      if(this.form.unitTown == ""){
        code = ''
      }else{
        code = this.form.unitTown + '-'
      }
      
      console.log('导出表格数据')
      const result = []
      getIntegralApplyList(
        '/integral/detailPage?page=' +
          page +
          '&size=' +
          size +
          '&postCode=' +
          code +
          '&sentryName=' +
          name +
          '&isPass=' +
          isPass +
          '&time=' +
          time
      )
        .then(res => {
          console.log(res)
          if (res.data.code == '1') {
            let resData = res.data.data.content
            console.log(res.data.data)
            for (let i = 0; i < resData.length; i++) {
              console.log(parseInt(100*(i/resData.length)))
              this.tableDataExportNum = parseInt(100*(i/resData.length)) 
              result.push({
                exchangeResult:
                  resData[i].isPass == 0
                    ? '待审批'
                    : (resData[i].isPass == 1 ? '已完成' : '已拒绝') || '未知',
                applyName: resData[i].sentryName || '未知',
                applyTime: resData[i].time || '未知',
                exchangeNum: Math.abs(resData[i].number) || '未知',
                handleName: resData[i].passSentryName || '未知',
                shopName: resData[i].shopName || '未知',
                goodsName: resData[i].goodsName || '未知',
                shopAddress: resData[i].address || '未知',
                shopBossName: resData[i].bossName || '未知',
                shopBossPhone: resData[i].bossPhone || '未知',
                passSentryName: resData[i].passSentryName,
                way: resData[i].way==0?"上报事件":(resData[i].way==1?"积分兑换":"哨长扣分")
              })
            }
            this.tableDataExportNum = 100
            this.tableDataExportColor = ''
            this.tableDataExportStatus = "success"
            setTimeout(this.tableDataExportModel = false,1000)
            console.log("下载完成")
            console.log(result)
            this.exportTableData = result
            this.isExportDataComplete = true
            this.exportDataTrue()
          } else if (res.data.code == '0') {
            this.tableDataExportStatus = "exception"
            this.$message({
              message: res.data.data,
              type: 'warning'
            })
          } else if (res.data.code == '-1') {
            this.tableDataExportStatus = "exception"
            this.$message({
              message: '网络错误!',
              type: 'error'
            })
          }
        })
        .catch(err => {
          console.log(err)
            this.tableDataExportStatus = "exception"
          this.$message({
            message: '网络错误',
            type: 'error'
          })
        })
    },


    indexMethod(index) {
      return index * 1
    },
    // 清空选择条件
    getSelectTown() {
      this.form.postLevel = ''
      this.form.postCode = ''
      this.form.eventState = ''
      this.form.sentryName = ''
      this.form.reportTime = ''
      this.currentPage = 1
      this.$root.count.$emit('pageReturnZero', this.currentPage)
      this.pageNum = 0
      console.log(this.form.unitTown)
      this.getTableData()
    },

    // getTownList获取乡镇县直单位
    getTownList() {
      const result = []
      console.log('获取乡镇县直单位')
      getTownList('/unit/all')
        .then(res => {
          if (res.data.code == '1') {
            let resData = res.data.data
            console.log(resData)
            for (let i = 0; i < resData.length; i++) {
              console.log('***')
              result.push({
                value: resData[i].code,
                label: resData[i].name
              })
            }
            console.log(result)
            this.selectTown = result
          } else if (res.data.code == '0') {
            this.$message({
              message: res.data.data,
              type: 'warning'
            })
          } else if (res.data.code == '-1') {
            this.$message({
              message: '网络错误!',
              type: 'error'
            })
          }
        })
        .catch(err => {
          console.log(err)
          this.$message({
            message: '网络错误',
            type: 'error'
          })
        })
    },

    // 获取哨所等级
    getPostLevel() {
      this.selectPostName = ''
      this.form.postCode = ''
      this.form.eventState = ''
      this.form.sentryName = ''
      this.form.reportTime = ''
      this.currentPage = 1
      this.$root.count.$emit('pageReturnZero', this.currentPage)
      this.pageNum = 0
      this.getAllPostListFun()
      this.getTableData()
    },

    getPostName() {
      this.currentPage = 1
      this.$root.count.$emit('pageReturnZero', this.currentPage)
      this.pageNum = 0
      this.getTableData()
    },
    // getReportSentryName 获取上报哨兵姓名
    // getReportSentryName() {
    // },
    // 积分审批状态
    getIntegralState() {},

    getIntegralTime() {},
    // 获取哨所列表getAllPostList
    getAllPostListFun() {
      const result = []
      let unitTown = this.form.unitTown
      let postLevel = this.form.postLevel
      console.log(unitTown)
      console.log(postLevel)
      console.log('获取哨所列表')
      getAllPostList('/post/all?code=' + unitTown + '-&grade=' + postLevel)
        .then(res => {
          if (res.data.code == '1') {
            let resData = res.data.data
            console.log(resData)
            for (let i = 0; i < resData.length; i++) {
              result.push({
                value: resData[i].code,
                label: resData[i].name
              })
            }
            console.log(result)
            this.selectPostName = result
          } else if (res.data.code == '0') {
            this.$message({
              message: res.data.data,
              type: 'warning'
            })
          } else if (res.data.code == '-1') {
            this.$message({
              message: '网络错误!',
              type: 'error'
            })
          }
        })
        .catch(err => {
          console.log(err)
          this.$message({
            message: '网络错误',
            type: 'error'
          })
        })
    },

    // 获取表格数据
    getTableData() {
      console.log('搜索关键字：' + JSON.stringify(this.form))
      let formData = this.form
      let code = ''
      if (
        formData.unitTown == '' &&
        formData.postLevel == '' &&
        formData.postCode == ''
      ) {
        code = ''
      } else if (
        formData.unitTown != '' &&
        formData.postLevel == '' &&
        formData.postCode == ''
      ) {
        code = formData.unitTown + '-'
      } else if (
        formData.unitTown != '' &&
        formData.postLevel != '' &&
        formData.postCode == ''
      ) {
        code = formData.unitTown + '-' + formData.postLevel
      } else if (
        formData.unitTown != '' &&
        formData.postLevel != '' &&
        formData.postCode != ''
      ) {
        code = formData.postCode
      }

      let name = formData.sentryName
      let isPass = formData.eventState
      let time = formData.reportTime
      let startTime = formData.reportStartTime
      console.log(parseInt(formData.reportEndTime.split("-")[2])+1)
      let arrayTime = new Array;
      arrayTime[0] = formData.reportEndTime.split("-")[0]
      arrayTime[1] = formData.reportEndTime.split("-")[1]
      arrayTime[2] = (parseInt(formData.reportEndTime.split("-")[2])+1)<10?("0"+(parseInt(formData.reportEndTime.split("-")[2])+1)):(parseInt(formData.reportEndTime.split("-")[2])+1)
      let endTime = arrayTime.join("-")
      if(time == null) time = ""
      if(startTime == null) time = ""
      if(endTime == null) time = ""
      let page = this.pageNum
      let size = this.pageStripNum
      let way = formData.way
      let passSentryCode = ''
      let passSentryName = formData.passSentryName
      console.log(time)
      const result = []
      getIntegralApplyList(
        '/integral/integralDetailPage?page=' +
          page +
          '&size=' +
          size +
          '&postCode=' +
          code +
          '&sentryName=' +
          name +
          '&isPass=' +
          isPass +
          '&startTime=' +
          startTime +
          '&endTime=' +
          endTime +
          '&way=' +
          way +
          '&passSentryCode=' +
          passSentryCode +
          '&passSentryName=' +
          passSentryName 
      )
        .then(res => {
          console.log(res)
          if (res.data.code == '1') {
            let resData = res.data.data.content
            console.log(res.data.data)
            this.tablePageNumber = res.data.data.totalElements
            this.$root.count.$emit('getTablePageNumber', this.tablePageNumber)
            for (let i = 0; i < resData.length; i++) {
              result.push({
                exchangeResult:
                  resData[i].isPass == 0
                    ? '待审批'
                    : (resData[i].isPass == 1 ? '已完成' : '已拒绝') || '未知',
                applyName: resData[i].sentryName || '未知',
                applyTime: resData[i].time || '未知',
                exchangeNum: resData[i].number || 0,
                handleName: resData[i].passSentryName || '未知',
                shopName: resData[i].shopName || '未知',
                goodsName: resData[i].goodsName || '未知',
                shopAddress: resData[i].address || '未知',
                shopBossName: resData[i].bossName || '未知',
                shopBossPhone: resData[i].bossPhone || '未知',
                id: resData[i].id,
                passSentryName: resData[i].passSentryName,
                way: resData[i].way==0?"上报事件":(resData[i].way==1?"积分兑换":"哨长扣分")
              })
            }
            console.log(result)
            this.exchangeTableData = result
          } else if (res.data.code == '0') {
            this.$message({
              message: res.data.data,
              type: 'warning'
            })
          } else if (res.data.code == '-1') {
            this.$message({
              message: '网络错误!',
              type: 'error'
            })
          }
        })
        .catch(err => {
          this.$message({
            message: '网络错误',
            type: 'error'
          })
        })
    },



    // 删除哨所提示框
    deleteIntegralApply(index, row) {
      console.log(index, row)
      let delectId = row.id
      console.log('delectId:' + delectId)
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.deleteIntegralApplySure(delectId)
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },

    // 删除哨所数据
    deleteIntegralApplySure(delectId) {
      console.log('delete')
      console.log(delectId)
      delectIntegralApply('/integral/delDetail?id=' + delectId)
        .then(res => {
          console.log(res)
          if (res.data.code == '1') {
            this.$message({
              type: 'success',
              message: '删除成功!'
            })
            this.getTableData()
          } else if (res.data.code == '0') {
            this.$message({
              message: res.data.data,
              type: 'warning'
            })
          } else if (res.data.code == '-1') {
            this.$message({
              message: '网络错误!',
              type: 'error'
            })
          }
        })
        .catch(err => {
          this.$message({
            message: '网络错误!',
            type: 'error'
          })
        })
    }
  }
}
</script>


<style lang='less'>
@import '../publicResources/common';
</style>


