<!-- 哨所模块 -->
<template>
  <div class="reportEventList outerDiv" id="reportEventList">
    <div class="reportEventListBox withinDiv">
      <el-container>
        <el-aside width="200px">
          <el-row class="asideDiv">
            <el-col :span="24" class="aside">
              <span class="logoSpan">
                              <i>综治哨所</i>
                              <i>后台管理系统</i>
                              </span>
              <!-- <img class="logo" src="../assets/logo.jpg" alt="logo"> -->
              <!-- 静态的资源 -->
              <div class="parentNav">
                <div class="nav">
                  <i class="icon iconfont icon-fl-renshi"></i>
                  <router-link to="/sentrylist">哨兵管理</router-link>
                </div>
                <div class="nav">
                  <i class="icon iconfont icon-suozaidi"></i>
                  <router-link to="/postlist">哨所管理</router-link>
                </div>
                <div class="nav">
                  <i class="icon iconfont icon-paihang"></i>
                  <router-link to="/Integralrank">积分排行</router-link>
                </div>
                <div class="nav">
                  <i class="icon iconfont icon-jifen3"></i>
                  <router-link to="/Integrallist">积分管理</router-link>
                </div>
                <div class="nav active">
                  <i class="icon iconfont icon-shijianchaxun-"></i>
                  <router-link to="/reporteventlist">事件管理</router-link>
                </div>
                <div class="nav" v-show="isShowEditUser">
                  <i class="icon iconfont icon-geren9"></i>
                  <router-link to="/userManage">管理中心</router-link>
                </div>
              </div>
            </el-col>
          </el-row>
        </el-aside>
        <el-container>
          <el-header style="height: 40px;">
            <my-header></my-header>
          </el-header>
          <el-main style="border-top: 1rem solid #eee;">
            <div class="sentryListMain">
              <!-- 显示上报事件详情 -->
              <el-dialog
                title="显示上报事件分布详情"
                :visible.sync="displayReportEvent"
                width="50%"
                center>
                <div id="myChart" :style="{width: '100%', height: '500px'}"></div>
              </el-dialog>
              <!-- 数据导出进度条 -->
              <el-dialog
                title="数据导出进度"
                :visible.sync="tableDataExportModel"
                width="30%"
                center>
                <el-progress :percentage="tableDataExportNum" :status="tableDataExportStatus" :color="tableDataExportColor" ></el-progress>
              </el-dialog>
              <!-- 搜索表单 -->
              <el-row class="searchForm">
                <el-form :inline="true" class="demo-form-inline" style="text-align: left;">
                  <el-form-item>
                    <el-select size="small" style="width:180px" v-model="form.unitTown" filterable clearable placeholder="请选择单位\乡镇场" @change="getSelectTown">
                      <el-option v-for="item in selectTown" :key="item.value" :label="item.label" :value="item.value">
                      </el-option>
                    </el-select>
                    <el-select size="small" style="width:140px" v-model="form.postLevel" filterable clearable placeholder="哨所等级" @change="getPostLevel">
                      <el-option label="一级哨所" value="1"></el-option>
                      <el-option label="二级哨所" value="2"></el-option>
                      <el-option label="三级哨所" value="3"></el-option>
                    </el-select>
                    <el-select size="small" style="width:180px" v-model="form.postCode" filterable clearable placeholder="哨所名称" @change="getPostName">
                      <el-option v-for="item in selectPostName" :key="item.value" :label="item.label" :value="item.value">
                      </el-option>
                    </el-select>
                    <el-input size="small" style="width:140px" placeholder="上报哨兵姓名" clearable v-model="form.sentryName" @change="getTableData">
                      <i slot="prefix" class="el-input__icon el-icon-search"></i>
                    </el-input>

                    <el-select size="small" style="width:140px" v-model="form.eventName" placeholder="事件类型" filterable clearable @change="getTableData">
                      <el-option v-for="item in selectEventName" :key="item.value" :label="item.label" :value="item.value">
                      </el-option>
                    </el-select>

                    <el-select size="small" style="width:100px;" v-model="form.eventState" @change="getTableData" placeholder="事件状态" clearable>
                      <el-option label="已完成" value="1"></el-option>
                      <el-option label="未完成" value="0"></el-option>
                    </el-select>

                    <el-date-picker size="small" style="width:140px" v-model="form.reportStartTime" @change="unlockReportEndTime" value-format="yyyy-MM-dd" align="right" type="date" placeholder="开始时间" :picker-options="pickerOptions">
                    </el-date-picker>
                    <el-date-picker size="small" style="width:140px" :disabled="isReportStartTime" @change="getTableData" v-model="form.reportEndTime" value-format="yyyy-MM-dd" align="right" type="date" placeholder="结束时间" :picker-options="pickerOptions">
                    </el-date-picker>
                    <el-button size="small" @click="getTableData" icon="el-icon-search" :loading="loading">查询</el-button>
                    <!-- 导出数据 -->
                    <el-button size="small" @click="export_Excel" icon="el-icon-download" :loading="loading">导出数据</el-button>
                    <el-button size="small" @click="displayReportEventClick" icon="el-icon-view" :loading="loading">查看分类详情</el-button>
                  </el-form-item>
                </el-form>
              </el-row>

              <!-- 数据表格 -->
              <el-row>
                <el-table :data="eventTableData" style="width: 100%">
                  <el-table-column type="expand">
                    <template slot-scope="props">
                      <el-form label-position="left" inline class="demo-table-expand">
                        <el-form-item label="事件状态">
                          <span>{{ props.row.eventState }}</span>
                        </el-form-item>
                        <el-form-item label="事件类型">
                          <span>{{ props.row.eventType }}</span>
                        </el-form-item>
                        <el-form-item label="事件上报人">
                          <span>{{ props.row.eventReportName }}</span>
                        </el-form-item>
                        <el-form-item label="事件上报时间">
                          <span>{{ props.row.eventReportTime }}</span>
                        </el-form-item>
                        <el-form-item label="事件内容">
                          <span>{{ props.row.eventContent }}</span>
                        </el-form-item>
                        <el-form-item label="事件附件图片">
                          <span>{{ props.row.eventImages }}</span>
                        </el-form-item>
                        <el-form-item label="事件审核人">
                          <span>{{ props.row.eventExamine }}</span>
                        </el-form-item>
                        <el-form-item label="事件审批人">
                          <span>{{ props.row.eventApproval }}</span>
                        </el-form-item>
                      </el-form>
                    </template>
                  </el-table-column>
                  <el-table-column label="序号" type="index" :index="indexMethod">
                  </el-table-column>
                  <el-table-column label="事件状态" prop="eventState" width="100">
                  </el-table-column>
                  <el-table-column label="事件类型" prop="eventType" width="200">
                  </el-table-column>
                  <el-table-column label="上报人姓名" prop="eventReportName" width="120">
                  </el-table-column>
                  <el-table-column label="上报人所在哨所" prop="eventReportPost">
                  </el-table-column>
                   <el-table-column label="上报人哨所等级" prop="eventReportLevel" width="120">
                  </el-table-column>
                  <el-table-column label="上报时间" prop="eventReportTime" width="180">
                  </el-table-column>
                  <el-table-column fixed="right" label="操作"  width="100">
                    <template slot-scope="scope">
                      <i class="el-icon-delete" @click.stop="deleteEventInfo(scope.$index, scope.row)"></i>
                    </template>
                  </el-table-column>
                </el-table>
              </el-row>
            </div>
            <my-paging></my-paging>
          </el-main>
        </el-container>
      </el-container>
    </div>
  </div>
</template>

<script>
import myAside from "../components/aside.vue";
import myPaging from "../components/paging.vue";
import myHeader from "../components/header.vue";
import {
  getTownList,
  getAllPostList,
  getReportEventList,
  delectReportEvent,
  uploadReportEvent,
  getAllReportEvent,
  getReportEventType
} from "../axios/api";

export default {
  data() {
    return {
      // 表格数据导出模态框
      tableDataExportModel: false,
      tableDataExportNum: 0,
      tableDataExportColor: "#8e71c7",
      tableDataExportStatus: "",
      formLabelWidth: "120",
      tableDataExportPostName: "",
      // 导出表格数据
      exportTableData: [],
      // 判断是否导出完毕
      isExportDataComplete: false,
      YearMonthDay: "", //当前年月日
      // 点击按钮加载状态
      loading: false,
      isShowEditUser: true,
      // 搜索表单
      form: {
        unitTown: "",
        postLevel: "",
        postCode: "",
        reportTime: "",
        sentryName: "",
        eventName: "",
        startTime: "",
        endTime: "",
        eventState: "",
        reportStartTime: "",
        reportEndTime: ""
      },
      pageNum: 0, //当前页数
      pageStripNum: 10, //每页的条数
      // 乡镇场、单位名称
      selectTown: [],
      // 哨所名称
      selectPostName: [],
      //哨兵姓名
      selectSentryName: [],
      // 上报事件类型
      selectEventName: [],

      eventTableData: [],

      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "今天",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "昨天",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "一周前",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      },
      isReportStartTime: true, //是否选择了开始时间
      displayReportEvent: false,
      reportEventCount:[],
      reportEventName:[]

    };
  },

  components: {
    myAside,
    myPaging,
    myHeader
  },

  computed: {},

  created() {
    let user = JSON.parse(sessionStorage.getItem("user"));
    if (user.grade == "1") this.isShowEditUser = true;
    // 获取乡镇县直单位
    this.getTownList();
    // 获取上报事件类型
    this.getAllReportEventFun();
    // 获取表格数据
    this.getTableData();
    //   控制翻页功能(非父子组件之间的通信)
    this.$root.page.$on("getEventListTable", value => {
      this.pageNum = value - 1;
      console.log(this.pageNum);
      this.getTableData();
    });
  },
  //离开当前页面后执行
  destroyed: function() {
    console.log("离开当前页面后执行");
    // 清空搜索条件对象
    for (let key in this.form) {
      this.form[key] = "";
    }
  },

  methods: {
    displayReportEventClick() {
      this.displayReportEvent = true
      let self = this
      setTimeout(function(){
        self.drawLine()
      },500)
    },
    // 解锁结束时间
    unlockReportEndTime() {
      if (this.form.reportStartTime != null) {
        this.isReportStartTime = false;
      }
    },
    // 导出表格数据
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]));
    },
    export_Excel() {
      this.$confirm("确定要导出表格数据么？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          let postCode = this.form.unitTown;
          console.log(postCode);
          this.getPostNameByPostCode(postCode);
          this.exportTableDataFun();
          this.tableDataExportModel = true;
          console.log(this.isExportDataComplete);
        })
        .catch(err => {
          console.log(err);
        });
    },

    // 导出函数
    exportDataTrue() {
      require.ensure([], () => {
        const { exportJsonToExcel } = require("../vendor/Export2Excel");
        const tHeader = [
          "事件状态",
          "事件类型",
          "事件上报人",
          "事件上报时间",
          "事件内容",
          "事件附件图片",
          "事件审核人",
          "事件审批人"
        ];
        const filterVal = [
          "eventState",
          "eventType",
          "eventReportName",
          "eventReportTime",
          "eventContent",
          "eventImages",
          "eventExamine",
          "eventApproval"
        ];
        const list = this.exportTableData;
        const data = this.formatJson(filterVal, list);
        exportJsonToExcel(
          tHeader,
          data,
          this.tableDataExportPostName + "上报事件列表"
        );
      });
    },

    getYearMonthDay() {},

    // 根据哨所编号查找哨所名称
    getPostNameByPostCode(postCode) {
      if (postCode == "") {
        this.tableDataExportPostName = "全部";
      } else {
        console.log(this.selectTown);
        for (let i in this.selectTown) {
          if (this.selectTown[i].value == parseInt(postCode)) {
            this.tableDataExportPostName = this.selectTown[i].label;
          }
        }
      }
    },

    // 导出表格数据http://116.62.138.218:8889/sentry/page?page=0&size=10000&idNumber=&name=&postCode=1-
    exportTableDataFun() {
      let page = 0;
      let size = 10000;
      let eventName = "";
      let name = "";
      let code = "";
      let time = "";
      if (this.form.unitTown == "") {
        code = "";
      } else {
        code = this.form.unitTown + "-";
      }

      console.log("导出表格数据");
      const result = [];
      getReportEventList(
        "/report/page?page=" +
          page +
          "&size=" +
          size +
          "&postCode=" +
          code +
          "&person=" +
          name +
          "&eventName=" +
          eventName +
          "&time=" +
          time
      )
        .then(res => {
          console.log(res);
          if (res.data.code == "1") {
            let resData = res.data.data.content;
            console.log(res.data.data);
            for (let i = 0; i < resData.length; i++) {
              console.log(parseInt(100 * (i / resData.length)));
              this.tableDataExportNum = parseInt(100 * (i / resData.length));
              let eventImages = "";
              if (resData[i].reportImageList.length == 0) {
                eventImages = "无";
              } else {
                eventImages = "有";
              }
              result.push({
                eventState:
                  resData[i].finish == 0
                    ? "进行中"
                    : resData[i].finish == 1 ? "已完成" : "已拒绝",
                eventType: resData[i].sort || "未知",
                eventReportName: resData[i].person || "未知",
                eventReportTime: resData[i].time,
                eventContent: resData[i].content || "未知",
                eventImages: eventImages,
                eventExamine: resData[i].twoPassSentryName || "未知",
                eventApproval: resData[i].onePassSentryName || "未知"
              });
            }
            this.tableDataExportNum = 100;
            this.tableDataExportColor = "";
            this.tableDataExportStatus = "success";
            setTimeout((this.tableDataExportModel = false), 1000);
            console.log("下载完成");
            console.log(result);
            this.exportTableData = result;
            this.isExportDataComplete = true;
            this.exportDataTrue();
          } else if (res.data.code == "0") {
            this.tableDataExportStatus = "exception";
            this.$message({
              message: res.data.data,
              type: "warning"
            });
          } else if (res.data.code == "-1") {
            this.tableDataExportStatus = "exception";
            this.$message({
              message: "网络错误!",
              type: "error"
            });
          }
        })
        .catch(err => {
          console.log(err);
          this.tableDataExportStatus = "exception";
          this.$message({
            message: "网络错误",
            type: "error"
          });
        });
    },

    indexMethod(index) {
      return index * 1;
    },
    // 获取上报事件类型
    getAllReportEventFun() {
      const result = [];
      console.log("获取上报事件类型");
      getAllReportEvent("/event/all")
        .then(res => {
          if (res.data.code == "1") {
            let resData = res.data.data;
            console.log(resData);
            for (let i = 0; i < resData.length; i++) {
              result.push({
                value: resData[i].name,
                label: resData[i].name
              });
            }
            console.log(result);
            this.selectEventName = result;
          } else if (res.data.code == "0") {
            this.$message({
              message: res.data.data,
              type: "warning"
            });
          } else if (res.data.code == "-1") {
            this.$message({
              message: "网络错误!",
              type: "error"
            });
          }
        })
        .catch(err => {
          console.log(err);
          this.$message({
            message: "网络错误",
            type: "error"
          });
        });
    },

    // 清空选择条件
    getSelectTown() {
      this.form.postLevel = "";
      this.form.postCode = "";
      this.form.eventState = "";
      this.form.sentryName = "";
      this.form.reportTime = "";
      this.currentPage = 1;
      this.$root.count.$emit("pageReturnZero", this.currentPage);
      this.pageNum = 0;
      console.log(this.form.unitTown);
      this.getTableData();
    },

    // getTownList获取乡镇县直单位
    getTownList() {
      const result = [];
      console.log("获取乡镇县直单位");
      getTownList("/unit/all")
        .then(res => {
          if (res.data.code == "1") {
            let resData = res.data.data;
            console.log(resData);
            for (let i = 0; i < resData.length; i++) {
              console.log("***");
              result.push({
                value: resData[i].code,
                label: resData[i].name
              });
            }
            console.log(result);
            this.selectTown = result;
          } else if (res.data.code == "0") {
            this.$message({
              message: res.data.data,
              type: "warning"
            });
          } else if (res.data.code == "-1") {
            this.$message({
              message: "网络错误!",
              type: "error"
            });
          }
        })
        .catch(err => {
          console.log(err);
          this.$message({
            message: "网络错误",
            type: "error"
          });
        });
    },

    // 获取哨所等级
    getPostLevel() {
      this.selectPostName = "";
      this.form.postCode = "";
      this.form.eventState = "";
      this.form.sentryName = "";
      this.form.reportTime = "";
      this.currentPage = 1;
      this.$root.count.$emit("pageReturnZero", this.currentPage);
      this.pageNum = 0;
      this.getAllPostListFun();
      this.getTableData();
    },

    getPostName() {
      this.currentPage = 1;
      this.$root.count.$emit("pageReturnZero", this.currentPage);
      this.pageNum = 0;
      this.getTableData();
    },
    // 获取哨所列表getAllPostList
    getAllPostListFun() {
      const result = [];
      let unitTown = this.form.unitTown;
      let postLevel = this.form.postLevel;
      console.log(unitTown);
      console.log(postLevel);
      console.log("获取哨所列表");
      getAllPostList("/post/all?code=" + unitTown + "-&grade=" + postLevel)
        .then(res => {
          if (res.data.code == "1") {
            let resData = res.data.data;
            console.log(resData);
            for (let i = 0; i < resData.length; i++) {
              result.push({
                value: resData[i].code,
                label: resData[i].name
              });
            }
            console.log(result);
            this.selectPostName = result;
          } else if (res.data.code == "0") {
            this.$message({
              message: res.data.data,
              type: "warning"
            });
          } else if (res.data.code == "-1") {
            this.$message({
              message: "网络错误!",
              type: "error"
            });
          }
        })
        .catch(err => {
          console.log(err);
          this.$message({
            message: "网络错误",
            type: "error"
          });
        });
    },

    getTableData() {

      console.log("搜索关键字：" + JSON.stringify(this.form));
      let formData = this.form;
      let code = "";
      if (
        formData.unitTown == "" &&
        formData.postLevel == "" &&
        formData.postCode == ""
      ) {
        code = "";
      } else if (
        formData.unitTown != "" &&
        formData.postLevel == "" &&
        formData.postCode == ""
      ) {
        code = formData.unitTown + "-";
      } else if (
        formData.unitTown != "" &&
        formData.postLevel != "" &&
        formData.postCode == ""
      ) {
        code = formData.unitTown + "-" + formData.postLevel;
      } else if (
        formData.unitTown != "" &&
        formData.postLevel != "" &&
        formData.postCode != ""
      ) {
        code = formData.postCode;
      }
      let finish = formData.eventState;
      let name = formData.sentryName;
      let eventName = formData.eventName;
      let time = formData.reportTime;
      let startTime = formData.reportStartTime;
      console.log(parseInt(formData.reportEndTime.split("-")[2]) + 1);
      let arrayTime = new Array();
      arrayTime[0] = formData.reportEndTime.split("-")[0];
      arrayTime[1] = formData.reportEndTime.split("-")[1];
      arrayTime[2] =
        parseInt(formData.reportEndTime.split("-")[2]) + 1 < 10
          ? "0" + (parseInt(formData.reportEndTime.split("-")[2]) + 1)
          : parseInt(formData.reportEndTime.split("-")[2]) + 1;
      let endTime = arrayTime.join("-");
      if (time == null) time = "";
      if (startTime == null) time = "";
      if (endTime == null) time = "";
      let page = this.pageNum;
      let size = this.pageStripNum;
      const result = [];
      getReportEventList(
        "/report/page?page=" +
          page +
          "&size=" +
          size +
          "&postCode=" +
          code +
          "&person=" +
          name +
          "&eventName=" +
          eventName +
          "&startTime=" +
          startTime +
          "&endTime=" +
          endTime +
          "&finish=" +
          finish
      )
        .then(res => {
          if (res.data.code == "1") {
            let resData = res.data.data.content;
            console.log(res.data.data);
            this.tablePageNumber = res.data.data.totalElements;
            this.$root.count.$emit("getTablePageNumber", this.tablePageNumber);
            for (let i = 0; i < resData.length; i++) {
              let eventImages = "";
              if (resData[i].reportImageList.length == 0) {
                eventImages = "无";
              } else {
                eventImages = "点击查看";
              }

              result.push({
                eventState:
                  resData[i].finish == 0
                    ? "进行中"
                    : resData[i].finish == 1 ? "已完成" : "已拒绝",
                eventType: resData[i].sort,
                eventReportName: resData[i].person,
                eventReportTime: resData[i].time,
                eventContent: resData[i].content,
                eventImages: eventImages,
                eventExamine: resData[i].twoPassSentryName,
                eventApproval: resData[i].onePassSentryName,
                eventReportPost: resData[i].postName,
                eventReportLevel: resData[i].postGrade,
                id: resData[i].id
              });
            }
            console.log(result);
            this.eventTableData = result;
          } else {
            this.$message({
              message: "获取数据失败",
              type: "warning"
            });
          }
        })
        .catch(err => {
          console.log(err);
          this.$message({
            message: "网络错误",
            type: "error"
          });
        });




        getReportEventType(
       '/report/eventCount?postCode=' +
          code +
          '&person=' +
          '' +
          '&eventName=' +
          '' +
          '&startTime=' +
          startTime +
          '&endTime=' +
          endTime +
          "&finish=" +
          finish
      )
        .then(res => {
          if (res.data.code == "1") {
            this.reportEventName = []
            this.reportEventCount = []
            let resData = res.data.data;
            console.log(res.data.data);
            for(let i in res.data.data){
              if(res.data.data[i].count == 0) continue
              this.reportEventName.push(res.data.data[i].name)
              this.reportEventCount.push({
                value: res.data.data[i].count,
                name: res.data.data[i].name,
              })
            }
            
          } else if (res.data.code == "0") {
            this.$message({
              message: res.data.data,
              type: "warning"
            });
          } else if (res.data.code == "-1") {
            this.$message({
              message: "网络错误!",
              type: "error"
            });
          }
        })
        .catch(err => {
          console.log(err);
          this.$message({
            message: "网络错误",
            type: "error"
          });
        });
    },

    drawLine(){
        let myChart = this.$echarts.init(document.getElementById('myChart'));
        myChart.setOption({
          tooltip : {
            trigger: 'item',
          },
          legend: {
            bottom: 20,
            left: 'center',
          },
          series : [
            {
              type: 'pie',
              radius : '55%',
              center: ['50%', '40%'],
              selectedMode: 'single',
              data: this.reportEventCount, // 扇形区域数据
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
        })
    },

    // 删除哨兵提示框
    deleteEventInfo(index, row) {
      console.log(index, row);
      let delectId = row.id;
      console.log("delectId:" + delectId);
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.deleteEventInfoSure(delectId);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },

    // 删除哨兵数据
    deleteEventInfoSure(delectId) {
      console.log("delete");
      console.log(delectId);
      delectReportEvent("/report/delete?id=" + delectId)
        .then(res => {
          console.log(res);
          if (res.data.code == "1") {
            this.$message({
              type: "success",
              message: "删除成功!"
            });
            // 重新更新列表数据
            this.getTableData();
          } else if (res.data.code == "0") {
            this.$message({
              message: res.data.data,
              type: "warning"
            });
          } else if (res.data.code == "-1") {
            this.$message({
              message: "网络错误!",
              type: "error"
            });
          }
        })
        .catch(err => {
          this.$message({
            message: "网络错误!",
            type: "error"
          });
        });
    }
  }
};
</script>


<style lang='less'>
@import "../publicResources/common";
</style>


