<!-- 哨所模块 -->
<template>
  <div class="postList outerDiv" id="postList">
    <div class="postListBox withinDiv">
      <el-container>
        <el-aside width="200px">
          <el-row class="asideDiv">
            <el-col :span="24" class="aside">
              <span class="logoSpan">
                              <i>综治哨所</i>
                              <i>后台管理系统</i>
                              </span>
              <!-- <img class="logo" src="../assets/logo.jpg" alt="logo"> -->
              <!-- 静态的资源 -->
              <div class="parentNav">
                <div class="nav">
                  <i class="icon iconfont icon-fl-renshi"></i>
                  <router-link to="/sentrylist">哨兵管理</router-link>
                </div>
                <div class="nav active">
                  <i class="icon iconfont icon-suozaidi"></i>
                  <router-link to="/postlist">哨所管理</router-link>
                </div>
                <div class="nav">
                  <i class="icon iconfont icon-paihang"></i>
                  <router-link to="/Integralrank">积分排行</router-link>
                </div>
                <div class="nav">
                  <i class="icon iconfont icon-jifen3"></i>
                  <router-link to="/Integrallist">积分管理</router-link>
                </div>
                <div class="nav">
                  <i class="icon iconfont icon-shijianchaxun-"></i>
                  <router-link to="/reporteventlist">事件管理</router-link>
                </div>
                <div class="nav" v-show="isShowEditUser">
                  <i class="icon iconfont icon-geren9"></i>
                  <router-link to="/userManage">管理中心</router-link>
                </div>
              </div>

            </el-col>
          </el-row>
        </el-aside>
        <el-container>
          <el-header style="height: 40px;">
            <my-header></my-header>
          </el-header>
          <el-main style="border-top: 1rem solid #eee;">
            <div class="sentryListMain">
              <!-- 数据导出进度条 -->
              <el-dialog
                title="数据导出进度"
                :visible.sync="tableDataExportModel"
                width="30%"
                center>
                <el-progress :percentage="tableDataExportNum" :status="tableDataExportStatus" :color="tableDataExportColor" ></el-progress>
              </el-dialog>
              <!-- 添加哨所 -->
              <el-dialog :title="modelName" custom-class="addOrEditPost addOrEdit" :visible.sync="addPostDataModel" width="60%">
                <el-form :model="addOrEditPostData" :inline="true" class="demo-form-inline" label-position="right">
                  <el-form-item label="哨所名称" :label-width="formLabelWidth">
                    <el-input size="small" v-model="addOrEditPostData.postName" clearable placeholder="请输入哨所名称"></el-input>
                  </el-form-item>
                  <el-form-item v-show="isShowPostUpload" label="哨所编号" :label-width="formLabelWidth">
                    <el-input size="small" v-model="addOrEditPostData.postNumber" clearable placeholder="请输入哨所编号"></el-input>
                  </el-form-item>
                  <el-form-item v-show="isShowPostUpload" label="单位\乡镇场" :label-width="formLabelWidth">
                    <el-select size="small" v-model="addOrEditPostData.postTown" filterable placeholder="请选择单位\乡镇场">
                      <el-option v-for="item in selectTown" :key="item.value" :label="item.label" :value="item.value">
                      </el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item v-show="isShowPostUpload" label="哨所等级" :label-width="formLabelWidth" clearable>
                    <el-select size="small" v-model="addOrEditPostData.postLevel" placeholder="请选择哨所等级" @change="getPostLevel">
                      <el-option label="一级哨所" value="1"></el-option>
                      <el-option label="二级哨所" value="2"></el-option>
                      <el-option label="三级哨所" value="3"></el-option>
                    </el-select>
                  </el-form-item>

                  <el-form-item v-show="isShowPostUpload" v-if="isShowUpPost" label="上级哨所" :label-width="formLabelWidth">
                    <el-select size="small" v-model="addOrEditPostData.superiorPostName" filterable placeholder="请选择上级哨所">
                      <el-option v-for="item in selectPostName" :key="item.value" :label="item.label" :value="item.value">
                      </el-option>
                    </el-select>
                  </el-form-item>

                </el-form>
                <span slot="footer" class="dialog-footer">
                  <el-button size="small" @click="addPostDataModel = false">取消</el-button>
                  <el-button size="small" type="primary" @click="SubmitPostData" :loading="loading">提交</el-button>
                </span>
              </el-dialog>

              <!-- 搜索表单 -->
              <el-row class="searchForm">
                <el-form :inline="true" class="demo-form-inline" style="text-align: left;">
                  <el-form-item>
                    <el-select size="small" style="width:180px" v-model="form.unitTown" filterable placeholder="请选择单位\乡镇场" clearable @change="getSelectTown">
                      <el-option v-for="item in selectTown" :key="item.value" :label="item.label" :value="item.value">
                      </el-option>
                    </el-select>
                    <el-select size="small" style="width:140px" v-model="form.postLevel" placeholder="哨所等级" clearable @change="getPostLevel">
                      <el-option label="一级哨所" value="1"></el-option>
                      <el-option label="二级哨所" value="2"></el-option>
                      <el-option label="三级哨所" value="3"></el-option>
                    </el-select>
                    <el-select size="small" style="width:280px" v-model="form.postCode" placeholder="哨所名称" filterable clearable @change="getPostName">
                      <el-option v-for="item in selectPostName" :key="item.value" :label="item.label" :value="item.value">
                      </el-option>
                    </el-select>
                    <el-input size="small" style="width:180px;display:none;" placeholder="请输入哨所编号" v-model="form.postNumber" clearable @change="getPostData">
                      <i slot="prefix" class="el-input__icon el-icon-search"></i>
                    </el-input>
                     <el-input size="small" style="width:180px;" placeholder="请输入哨所名称" v-model="formPostName" clearable @change="getPostData">
                      <i slot="prefix" class="el-input__icon el-icon-search"></i>
                    </el-input>
                    
                    <el-button size="small" @click="getPostData" icon="el-icon-search" :loading="loading">查询</el-button>
                    <!-- 导出数据 -->
                    <el-button size="small" @click="export_Excel" icon="el-icon-download" :loading="loading">导出数据</el-button>
                    <el-button size="small" style="position: absolute;right: 0;top: 4px;" @click="addPostDataModelTrue" icon="el-icon-plus">添加哨所</el-button>
                  </el-form-item>
                </el-form>
              </el-row>

              <!-- 数据表格 -->
              <el-row>
                <el-table :data="postTableData" style="width: 100%">
                  <el-table-column label="序号" type="index" :index="indexMethod">
                  </el-table-column>
                  <el-table-column label="哨所名称" prop="postName">
                  </el-table-column>
                  <el-table-column label="哨所编号" prop="postNumber">
                  </el-table-column>
                  <el-table-column label="哨所等级" prop="postLevel">
                  </el-table-column>
                  <el-table-column fixed="right" label="操作" width="120">
                    <template slot-scope="scope">
                      <i class="el-icon-edit" @click.stop="editPostInfo(scope.$index, scope.row)"></i>
                      <i class="el-icon-delete" @click.stop="deletePostInfo(scope.$index, scope.row)"></i>
                    </template>
                  </el-table-column>
                </el-table>
              </el-row>
            </div>
            <my-paging></my-paging>
          </el-main>
        </el-container>
      </el-container>
    </div>
  </div>
</template>

<script>
import myAside from '../components/aside.vue'
import myPaging from '../components/paging.vue'
import myHeader from '../components/header.vue'
import {
  getTownList,
  getAllPostList,
  getPostList,
  delectPost,
  uploadPost,
  addPost
} from '../axios/api'
export default {
  data() {
    return {
      formPostName: '',
      isShowEditUser: true,
      // 点击按钮加载状态
      loading: false,
      //添加、编辑哨所模态框
      addPostDataModel: false,
      modelName: '添加哨所',
      formLabelWidth: '120',
      // 表格数据导出模态框
      tableDataExportModel: false,
      tableDataExportNum: 0,
      tableDataExportColor: '#8e71c7',
      tableDataExportStatus:"",
      formLabelWidth: '120',
      tableDataExportPostName: '',
      // 导出表格数据
      exportTableData: [],
      // 判断是否导出完毕
      isExportDataComplete: false,
      YearMonthDay: '', //当前年月日
      // 添加、编辑哨所数据
      addOrEditPostData: {
        postName: '',
        postNumber: '',
        postTown: '',
        postLevel: '',
        superiorPostName: ''
      },
      pageNum: 0, //当前页数
      pageStripNum: 10, //每页的条数
      isShowPostUpload: true,
      isShowUpPost: false,
      isAddType: true,
      postId: 0,
      postCode: 0,
      // 搜索表单
      form: {
        unitTown: '',
        postLevel: '',
        postName: '',
        postNumber: '',
        postCode: ''
      },
      // 乡镇场、单位名称
      selectTown: [],
      // 哨所名称
      selectPostName: [],
      // 上级哨所
      superiorPostName: [],
      // 哨所表格数据
      postTableData: [],
      // reload : true
    }
  },

  components: {
    myAside,
    myPaging,
    myHeader
  },

  computed: {},

  created() {

    let user = JSON.parse(sessionStorage.getItem('user'));
    if(user.grade == '1') this.isShowEditUser=true;
    // 获取乡镇县直单位
    this.getTownList()
    // 获取表格数据
    this.getPostData(this.form)
    //   控制翻页功能(非父子组件之间的通信)
    this.$root.page.$on('getPostListTable', value => {
      this.pageNum = value - 1
      console.log(this.pageNum)
      this.getPostData(this.form)
    })
    // 深拷贝了一份数据模板,用于清空表单数据
    this.addOrEditPostDataEmpty = JSON.parse(
      JSON.stringify(this.addOrEditPostData)
    )

  },

  mounted() {
    // if(this.reload=="true"){
    //   this.reload = false
    //   location.reload()
    // }
    
  },
  //离开当前页面后执行
  destroyed: function () {
    console.log("离开当前页面后执行")
      // 清空搜索条件对象
    for(let key in this.form){
      this.form[key] = ""
    }
  },

  methods: {
    // 导出表格数据
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]))
    },
    export_Excel() {
      this.$confirm('确定要导出表格数据么？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          let postCode = this.form.unitTown
          console.log(postCode)
          this.getPostNameByPostCode(postCode)
          this.exportTableDataFun()
          this.tableDataExportModel = true
          console.log(this.isExportDataComplete)
        })
        .catch((err) => {
          console.log(err)
        })
    },

    // 导出函数
    exportDataTrue() {
      require.ensure([], () => {
        const { exportJsonToExcel } = require('../vendor/Export2Excel')
        const tHeader = [
          '哨所名称',
          '哨所编号',
          '哨所等级'
        ]
        const filterVal = [
          'postName',
          'postNumber',
          'postLevel'
        ]
        const list = this.exportTableData
        const data = this.formatJson(filterVal, list)
        exportJsonToExcel(tHeader, data, this.tableDataExportPostName+'哨所列表')
      })
    },

    getYearMonthDay(){

    },

    // 根据哨所编号查找哨所名称
    getPostNameByPostCode(postCode){
      if(postCode == ''){
        this.tableDataExportPostName = "全部"
      }else{
        console.log(this.selectTown)
        for(let i in this.selectTown){
          if(this.selectTown[i].value == parseInt(postCode)){
            this.tableDataExportPostName = this.selectTown[i].label
          }
        }
      }
    },

    // 导出表格数据http://116.62.138.218:8889/sentry/page?page=0&size=10000&idNumber=&name=&postCode=1-
    exportTableDataFun() {
      let page = 0
      let size = 10000
      let grade = ''
      let code = ''
      if(this.form.unitTown == ""){
        code = ''
      }else{
        code = this.form.unitTown + '-'
      }
      
      console.log('导出表格数据')
      const result = []
      getPostList(
        '/post/page?page=' +
          page +
          '&size=' +
          size +
          '&code=' +
          code +
          '&grade=' +
          grade
      )
        .then(res => {
          console.log(res)
          if (res.data.code == '1') {
            let resData = res.data.data.content
            console.log(res.data.data)
            for (let i = 0; i < resData.length; i++) {
              console.log(parseInt(100*(i/resData.length)))
              this.tableDataExportNum = parseInt(100*(i/resData.length)) 
              result.push({
                postName: resData[i].name,
                postNumber: resData[i].number || '未知',
                postLevel: resData[i].grade || '未知',
              })
            }
            this.tableDataExportNum = 100
            this.tableDataExportColor = ''
            this.tableDataExportStatus = "success"
            setTimeout(this.tableDataExportModel = false,1000)
            console.log("下载完成")
            console.log(result)
            this.exportTableData = result
            this.isExportDataComplete = true
            this.exportDataTrue()
          } else if (res.data.code == '0') {
            this.tableDataExportStatus = "exception"
            this.$message({
              message: res.data.data,
              type: 'warning'
            })
          } else if (res.data.code == '-1') {
            this.tableDataExportStatus = "exception"
            this.$message({
              message: '网络错误!',
              type: 'error'
            })
          }
        })
        .catch(err => {
          console.log(err)
            this.tableDataExportStatus = "exception"
          this.$message({
            message: '网络错误',
            type: 'error'
          })
        })
    },


    addPostDataModelTrue(){
      this.addPostDataModel = true
      this.modelName = "添加哨所"
      this.isShowPostUpload = true
      this.isAddType = true
      this.addOrEditPostData = JSON.parse(
        JSON.stringify(this.addOrEditPostDataEmpty)
      )
    },
    indexMethod(index) {
      return index * 1
    },
    // 清空选择条件
    getSelectTown() {
      this.form.postLevel = ''
      this.form.postCode = ''
      this.form.postName = ''
      this.form.postNumber = ''
      this.pageNum = 0
      this.currentPage = 1
      this.$root.count.$emit('pageReturnZero', this.currentPage)
      this.getPostData(this.form)
    },
    // getTownList获取乡镇县直单位
    getTownList() {
      const result = []
      console.log('获取乡镇县直单位')
      getTownList('/unit/all')
        .then(res => {
          if (res.data.code == '1') {
            let resData = res.data.data
            console.log(resData)
            for (let i = 0; i < resData.length; i++) {
              result.push({
                value: resData[i].code,
                label: resData[i].name
              })
            }
            console.log(result)
            this.selectTown = result
          } else if (res.data.code == '0') {
            this.$message({
              message: res.data.data,
              type: 'warning'
            })
          } else if (res.data.code == '-1') {
            this.$message({
              message: '网络错误!',
              type: 'error'
            })
          }
        })
        .catch(err => {
          console.log(err)
          this.$message({
            message: '网络错误',
            type: 'error'
          })
        })
    },

    // 获取哨所等级
    getPostLevel() {
      console.log(this.form)
      this.selectPostName = ''
      this.form.postCode = ''
      this.form.postNumber = ''
      this.form.postName = ''
      this.pageNum = 0
      this.currentPage = 1
      this.$root.count.$emit('pageReturnZero', this.currentPage)
      this.getAllPostListFun()
      this.getPostData(this.form)
    },
    getPostName() {
      this.pageNum = 0
      this.currentPage = 1
      this.$root.count.$emit('pageReturnZero', this.currentPage)
      this.getPostData(this.form)
    },
    // 获取哨所列表getAllPostList
    getAllPostListFun() {
      const result = []
      let addOrEditPostData = this.addOrEditPostData
      let unitTown = this.form.unitTown || addOrEditPostData.postTown
      let postLevel = this.form.postLevel || addOrEditPostData.postLevel
      console.log(addOrEditPostData.postLevel)
      if (this.modelName == '添加哨所' && addOrEditPostData.postLevel > 1) {
        console.log('添加哨所')
        postLevel = parseInt(postLevel) - 1
        this.isShowUpPost = true
        this.addOrEditPostData.superiorPostName = ''
        console.log(this.isShowUpPost)
      } else if (addOrEditPostData.postLevel == 1) {
        this.isShowUpPost = false
        this.addOrEditPostData.superiorPostName = unitTown + '-0-0'
      }
      console.log(unitTown)
      console.log(postLevel)
      console.log('获取哨所列表')
      getAllPostList('/post/all?code=' + unitTown + '-&grade=' + postLevel)
        .then(res => {
          if (res.data.code == '1') {
            let resData = res.data.data
            console.log(resData)
            for (let i = 0; i < resData.length; i++) {
              result.push({
                value: resData[i].code,
                label: resData[i].name
              })
            }
            console.log(result)
            this.selectPostName = result
          } else if (res.data.code == '0') {
            this.$message({
              message: res.data.data,
              type: 'warning'
            })
          } else if (res.data.code == '-1') {
            this.$message({
              message: '网络错误!',
              type: 'error'
            })
          }
        })
        .catch(err => {
          console.log(err)
          this.$message({
            message: '网络错误',
            type: 'error'
          })
        })
    },

    // 获取哨所表格数据
    getPostData(formData) {
      console.log('搜索关键字：' + JSON.stringify(this.form))
      
      // let formData = this.form
      console.log(formData.unitTown)
      console.log(formData.postLevel)
      if(formData.unitTown == undefined) formData.unitTown = ""
      if(formData.postLevel == undefined) formData.postLevel = ""
      let grade = formData.postLevel
      let code = ''
      if(formData.unitTown == '' && formData.postLevel == '' && formData.postCode == ''){
          code = ''
      }else if(formData.unitTown != '' && formData.postLevel == '' && formData.postCode == ''){
        code = formData.unitTown+'-'
      }else if(formData.unitTown != '' && formData.postLevel != '' && formData.postCode == ''){
         code = formData.unitTown+'-'+formData.postLevel
      }else if(formData.unitTown != '' && formData.postLevel != '' && formData.postCode != ''){
          code = formData.postCode
      }
      let page = this.pageNum
      let size = this.pageStripNum
      let name = this.formPostName
      let number = ''
      const result = []
      getPostList(
        '/post/page?page=' +page +'&size=' +size +'&code=' +code +'&grade=' +grade+'&name=' +
          name +
          '&number=' +
          number
      )
        .then(res => {
          console.log(res)
          if (res.data.code == '1') {
            let resData = res.data.data.content
            console.log(resData)
            this.tablePageNumber = res.data.data.totalElements
            this.$root.count.$emit('getTablePageNumber', this.tablePageNumber)
            for (let i = 0; i < resData.length; i++) {
              result.push({
                postName: resData[i].name || '未知',
                postNumber: resData[i].number || '未知',
                postLevel: resData[i].grade || '未知',
                postWhereTown: '未知',
                postSentryNum: '未知',
                postSuperiorPost: '未知',
                postReportNum: '未知',
                postId: resData[i].id,
                postCode: resData[i].code
              })
            }
            console.log(result)
            this.postTableData = result
          } else if (res.data.code == '0') {
            this.$message({
              message: res.data.data,
              type: 'warning'
            })
          } else if (res.data.code == '-1') {
            this.$message({
              message: '网络错误!',
              type: 'error'
            })
          }
        })
        .catch(err => {
          console.log(err)
          this.$message({
            message: '网络错误',
            type: 'error'
          })
        })
    },

    // 编辑哨所
    editPostInfo(index, row) {
      console.log(index, row)
      this.modelName = '编辑哨所'
      this.isShowPostUpload = false
      this.addPostDataModel = true
      this.isAddType = false
      this.postId = row.postId
      this.postCode = row.postCode
      this.addOrEditPostData.postName = row.postName
      this.addOrEditPostData.postNumber = row.postNumber
    },

    // 提交---哨兵的编辑和提交
    SubmitPostData() {
      console.log(this.addOrEditPostData)
      let addPostData = this.addOrEditPostData
      let code = addPostData.postTown + '-' + addPostData.postLevel
      if (this.isAddType) {
        let data = {
          name: this.addOrEditPostData.postName,
          grade: this.addOrEditPostData.postLevel,
          number: this.addOrEditPostData.postNumber,
          type: this.addOrEditPostData.postNumber,
          code: code,
          superPostCode: this.addOrEditPostData.superiorPostName,
          zzBank: ''
        }
        console.log(data)
        console.log('add')
        addPost('/post/add', data)
          .then(res => {
            console.log(res)
            if (res.data.code == '1') {
              this.$message({
                type: 'success',
                message: '添加哨所信息成功!'
              })
              this.addOrEditPostData = JSON.parse(
                JSON.stringify(this.addOrEditPostDataEmpty)
              )
              this.addPostDataModel = false
              this.modelName = '添加哨所'
              this.isShowPostUpload = true
              // 重新更新列表数据
              this.getPostData(this.form)
            } else if (res.data.code == '0') {
              this.$message({
                message: res.data.data,
                type: 'warning'
              })
            } else if (res.data.code == '-1') {
              this.$message({
                message: '网络错误!',
                type: 'error'
              })
            }
          })
          .catch(err => {
            this.$message({
              message: '网络错误!',
              type: 'error'
            })
          })
      } else {
        let data = {
          id: this.postId,
          name: this.addOrEditPostData.postName,
          number: this.addOrEditPostData.postNumber,
          type: this.addOrEditPostData.postNumber,
          zzBank: ''
          // postCode: this.postCode,
        }
        console.log(data)
        console.log('upload')
        uploadPost('/post/update', data)
          .then(res => {
            console.log(res)
            if (res.data.code == '1') {
              this.$message({
                type: 'success',
                message: '修改哨所信息成功!'
              })
              this.addOrEditPostData = JSON.parse(
                JSON.stringify(this.addOrEditPostDataEmpty)
              )
              this.addPostDataModel = false
              this.isShowPostUpload = true
              // 重新更新病例列表数据
              this.getPostData(this.form)
            } else if (res.data.code == '0') {
              this.$message({
                message: res.data.data,
                type: 'warning'
              })
            } else if (res.data.code == '-1') {
              this.$message({
                message: '网络错误!',
                type: 'error'
              })
            }
          })
          .catch(err => {
            console.log(err)
            this.$message({
              message: '网络错误!',
              type: 'error'
            })
          })
      }
    },

    // 删除哨所提示框
    deletePostInfo(index, row) {
      console.log(index, row)
      let delectId = row.postId
      console.log('delectId:' + delectId)
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.deletePostInfoSure(delectId)
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },

    // 删除哨所数据
    deletePostInfoSure(delectId) {
      console.log('delete')
      console.log(delectId)
      delectPost('/post/del?id=' + delectId)
        .then(res => {
          console.log(res)
          if (res.data.code == '1') {
            this.$message({
              type: 'success',
              message: '删除成功!'
            })
            // 重新更新病例列表数据
            this.getPostData(this.form)
          } else if (res.data.code == '0') {
            this.$message({
              message: res.data.data,
              type: 'warning'
            })
          } else if (res.data.code == '-1') {
            this.$message({
              message: '网络错误!',
              type: 'error'
            })
          }
        })
        .catch(err => {
          this.$message({
            message: '网络错误!',
            type: 'error'
          })
        })
    }
  }
}
</script>


<style lang='less'>
@import '../publicResources/common';
