// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import routes from './router'
import ElementUI from 'element-ui'
import axios from 'axios'
import echarts from 'echarts'
// 引入样式
import 'element-ui/lib/theme-chalk/index.css'
import './publicResources/base.less'

Vue.config.productionTip = false


Vue.use(ElementUI)
Vue.use(VueRouter)

// 路由定义
const router = new VueRouter({
  mode: 'history',
  routes
})
Vue.prototype.$echarts = echarts;
Vue.prototype.$ajax = axios; //将axios挂载到Vue实例中的$ajax上面,在项目中的任何位置通过this.$http使用
Vue.prototype.hostUrl = 'http://116.62.138.218:8889';   //设置全局变量  通过this.hostUrl访问(上线地址)
// Vue.prototype.hostUrl = 'http://192.168.31.222:8889'; //设置全局变量  通过this.hostUrl访问（本地地址）


// 全局钩子（异步工作）每次刷新页面或者更换页面都会执行这个，判断是否有登录的权限
router.beforeEach((to, from, next) => {
  console.log("页面访问路径："+to.path)
  
  // 如果第一次进入登录界面或者回到登陆界面，则清除user信息
  if (to.path == '/index' || to.path == '/') {
    sessionStorage.removeItem('user');
  }
  let user = JSON.parse(sessionStorage.getItem('user'));

  // console.log("用户登录信息："+user)
  if (!user && to.path != '/index') {
    next({
      path: '/index'
    })
  }else {
    next(()=>{
      console.log("刷新页面")
    })
  }
})


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>',
  data: {
    // 空的实例放到根组件下，所有的子组件都能调用
    page: new Vue(),
    count: new Vue(),
  }
})



