<!-- 积分模块 -->
<template>
    <div class="userManage outerDiv" id="userManage">
        <div class="userManageBox withinDiv">
            <el-container>
                <el-aside width="200px">
                    <el-row class="asideDiv">
                        <el-col :span="24" class="aside">
                            <span class="logoSpan">
                              <i>综治哨所</i>
                              <i>后台管理系统</i>
                              </span>
                            <!-- <img class="logo" src="../assets/logo.jpg" alt="logo"> -->
                            <!-- 静态的资源 -->
                            <div class="parentNav">
                                <div class="nav">
                                    <i class="icon iconfont icon-fl-renshi"></i>
                                    <router-link to="/sentrylist">哨兵管理</router-link>
                                </div>
                                <div class="nav">
                                    <i class="icon iconfont icon-suozaidi"></i>
                                    <router-link to="/postlist">哨所管理</router-link>
                                </div>
                                <div class="nav">
                                    <i class="icon iconfont icon-paihang"></i>
                                    <router-link to="/Integralrank">积分排行</router-link>
                                </div>
                                <div class="nav">
                                    <i class="icon iconfont icon-jifen3"></i>
                                    <router-link to="/Integrallist">积分管理</router-link>
                                </div>
                                <div class="nav">
                                    <i class="icon iconfont icon-shijianchaxun-"></i>
                                    <router-link to="/reporteventlist">事件管理</router-link>
                                </div>
                                <div class="nav active" v-show="isShowEditUser">
                                    <i class="icon iconfont icon-geren9"></i>
                                    <router-link to="/userManage">管理中心</router-link>
                                </div>
                            </div>
                        </el-col>
                    </el-row>
                </el-aside>
                <el-container>
                    <el-header style="height: 40px;">
                        <my-header></my-header>
                    </el-header>
                    <el-main style="border-top: 1rem solid #eee;">
                        <div class="sentryListMain">
                            <!-- 显示上报事件详情 -->
                            <el-dialog
                              title="显示上报事件分布详情"
                              :visible.sync="displayReportEvent"
                              width="50%"
                              center>
                              <el-form-item>
                                  <el-select size="small" style="width:180px" v-model="form.unitTown" filterable clearable placeholder="请选择单位\乡镇场" @change="getReportEventTypeData">
                                    <el-option v-for="item in selectTown" :key="item.value" :label="item.label" :value="item.value">
                                    </el-option>
                                  </el-select>
                                   <el-date-picker size="small" style="width:140px" v-model="form.reportStartTime" @change="unlockReportEndTime" value-format="yyyy-MM-dd" align="right" type="date" placeholder="开始时间" :picker-options="pickerOptions">
                                  </el-date-picker>
                                  <el-date-picker size="small" style="width:140px" :disabled="isReportStartTime" @change="getReportEventTypeData" v-model="form.reportEndTime" value-format="yyyy-MM-dd" align="right" type="date" placeholder="结束时间" :picker-options="pickerOptions">
                                  </el-date-picker>
                              </el-form-item>
                              <div id="myChart" :style="{width: '300px', height: '300px'}"></div>
                              <span slot="footer" class="dialog-footer">
                                <el-button @click="displayReportEvent = false">取 消</el-button>
                                <el-button type="primary" @click="displayReportEvent = false">确 定</el-button>
                              </span>
                            </el-dialog>
                            <!-- 显示空缺的哨兵编号 -->
                            <el-dialog
                              title="空缺的哨兵编号"
                              :visible.sync="displayVacancySentryId"
                              width="50%"
                              center>
                              <span style="width: 10%;display: inline-block;margin-bottom: 10px;" v-for="item in VacancySentryIdList" :key="item">{{(Array(5).join(0) + item).slice(-5)}}</span>
                              <span slot="footer" class="dialog-footer">
                                <el-button @click="displayVacancySentryId = false">取 消</el-button>
                                <el-button type="primary" @click="displayVacancySentryId = false">确 定</el-button>
                              </span>
                            </el-dialog>
                            <!-- 添加\修改用户 -->
                            <el-dialog :title="modelName" custom-class="addOrEditSentry addOrEdit" :visible.sync="addUserDataModel" width="70%">
                                <el-form :model="addOrEditUserData" :inline="true" class="demo-form-inline" label-position="right" style="width: 100%;">
                                    <el-form-item label="昵称" :label-width="formLabelWidth">
                                        <el-input size="small" v-model="addOrEditUserData.nickName" clearable placeholder="请输入昵称"></el-input>
                                    </el-form-item>
                                    <el-form-item label="等级" :label-width="formLabelWidth">
                                        <el-select size="small" v-model="addOrEditUserData.grade" placeholder="请选择等级">
                                            <el-option label="1" value="1"></el-option>
                                            <el-option label="2" value="2"></el-option>
                                            <el-option label="3" value="3"></el-option>
                                        </el-select>
                                    </el-form-item>
                                    <el-form-item v-show="isShowUserUpload" label="账号" :label-width="formLabelWidth">
                                        <el-input size="small" v-model="addOrEditUserData.userName" clearable placeholder="请输入账号"></el-input>
                                    </el-form-item>
                                    <el-form-item label="密码" :label-width="formLabelWidth">
                                        <el-input size="small" v-model="addOrEditUserData.password" clearable placeholder="请输入密码"></el-input>
                                    </el-form-item>
                                    <el-form-item v-show="isShowUserUpload" label="头像地址" :label-width="formLabelWidth">
                                        <el-input size="small" v-model="addOrEditUserData.headImage" clearable placeholder="请输入在线头像地址"></el-input>
                                    </el-form-item>

                                </el-form>
                                <span slot="footer" class="dialog-footer">
                                    <el-button size="small" @click="addUserDataModel = false">取消</el-button>
                                    <el-button size="small" type="primary" @click="SubmitUserData" :loading="loading">提交</el-button>
                                </span>
                            </el-dialog>

                            <!-- 搜索表单 -->
                            <el-row class="searchForm">
                                <el-form :inline="true" class="demo-form-inline" style="text-align: left;">
                                    <el-form-item>
                                        <el-button size="small" style="position: absolute;right: 350px;top: 4px;display:none;" @click="displayReportEvent = true">查看上报事件分布</el-button>
                                        <el-button size="small" style="position: absolute;right: 150px;top: 4px;" @click="displayVacancySentryId = true">查看空缺哨兵编号列表</el-button>
                                        <el-button size="small" style="position: absolute;right: 0;top: 4px;" @click="addUserDataModelTrue" icon="el-icon-plus">添加用户</el-button>
                                    </el-form-item>
                                </el-form>
                            </el-row>
                            <!-- 数据表格 -->
                            <el-row>
                                <el-table :data="exchangeTableData" style="width: 100%">
                                    <el-table-column label="序号" type="index" :index="indexMethod">
                                    </el-table-column>
                                    <el-table-column label="昵称" prop="nickName">
                                    </el-table-column>
                                    <el-table-column label="等级" prop="grade">
                                    </el-table-column>
                                    <el-table-column label="头像地址" prop="headImage">
                                    </el-table-column>
                                    <el-table-column label="用户名" prop="userName">
                                    </el-table-column>
                                    <el-table-column label="密码" prop="password">
                                    </el-table-column>
                                    <el-table-column fixed="right" label="操作" width="120">
                                        <template slot-scope="scope">
                                            <i class="el-icon-edit" @click.stop="editUserInfo(scope.$index, scope.row)"></i>
                                            <i class="el-icon-delete" @click.stop="deleteUserInfo(scope.$index, scope.row)"></i>
                                        </template>
                                    </el-table-column>
                                </el-table>
                            </el-row>
                        </div>
                    </el-main>
                </el-container>
            </el-container>
        </div>
    </div>
</template>

<script>
import myHeader from "../components/header.vue";
import {
  getAllUsers,
  addUsers,
  uploadUsers,
  delectUsers,
  getDelectSentry,
  getReportEventType,
  getTownList
} from "../axios/api";
export default {
  data() {
    return {
      displayReportEvent: false,
      isShowEditUser: true,
      // 点击按钮加载状态
      loading: false,
      addUserDataModel: false,
      formLabelWidth: "120",
      // 添加、编辑用户
      addOrEditUserData: {
        userName: "",
        password: "",
        grade: "",
        nickName: "",
        headImage: ""
      },
      modelName: "添加用户",
      isShowUserUpload: true,
      isAddType: true,
      editUserId: "",
      exchangeTableData: [],
      VacancySentryIdList: {},
      displayVacancySentryId: false,
      form: {
        unitTown: "",
        reportStartTime: "",
        reportEndTime: ""
      },
      selectTown: [],
      YearMonthDay: '', //当前年月日
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "今天",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "昨天",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "一周前",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      },
      isReportStartTime: true //是否选择了开始时间
    };
  },

  components: {
    myHeader
  },

  computed: {},

  created() {
    let user = JSON.parse(sessionStorage.getItem("user"));
    if (user.grade == "1") this.isShowEditUser = true;
    // 获取表格数据
    this.getTableData();
    this.addOrEditUserDataEmpty = JSON.parse(
      JSON.stringify(this.addOrEditUserData)
    );
    this.getTownList()
    this.getVacancySentryIdData();
    this.getReportEventTypeData();
  },
  methods: {
    // 解锁结束时间
    unlockReportEndTime() {
      if (this.form.reportStartTime != null) {
        this.isReportStartTime = false;
      }
    },
    // getTownList获取乡镇县直单位
    getTownList() {
      const result = [];
      console.log("获取乡镇县直单位");
      getTownList("/unit/all")
        .then(res => {
          if (res.data.code == "1") {
            let resData = res.data.data;
            console.log(resData);
            for (let i = 0; i < resData.length; i++) {
              console.log("***");
              result.push({
                value: resData[i].code,
                label: resData[i].name
              });
            }
            console.log(result);
            this.selectTown = result;
          } else if (res.data.code == "0") {
            this.$message({
              message: res.data.data,
              type: "warning"
            });
          } else if (res.data.code == "-1") {
            this.$message({
              message: "网络错误!",
              type: "error"
            });
          }
        })
        .catch(err => {
          console.log(err);
          this.$message({
            message: "网络错误",
            type: "error"
          });
        });
    },
    addUserDataModelTrue() {
      this.addUserDataModel = true;
      this.modelName = "添加用户";
      this.isShowUserUpload = true;
      this.isAddType = true;
      this.addOrEditUserData = JSON.parse(
        JSON.stringify(this.addOrEditUserDataEmpty)
      );
    },

    indexMethod(index) {
      return index * 1;
    },

    getTableData() {
      const result = [];
      getAllUsers("/admin/all")
        .then(res => {
          console.log(res);
          if (res.data.code == "1") {
            let resData = res.data.data;
            console.log(res.data.data);
            for (let i = 0; i < resData.length; i++) {
              result.push({
                nickName: resData[i].nickName,
                grade: resData[i].grade,
                headImage: resData[i].headImage,
                userName: resData[i].userName,
                password: resData[i].password,
                id: resData[i].id
              });
            }
            console.log(result);
            this.exchangeTableData = result;
          } else if (res.data.code == "0") {
            this.$message({
              message: res.data.data,
              type: "warning"
            });
          } else if (res.data.code == "-1") {
            this.$message({
              message: "网络错误!",
              type: "error"
            });
          }
        })
        .catch(err => {
          console.log(err);
          this.$message({
            message: "网络错误",
            type: "error"
          });
        });
    },

    // 获取空缺哨兵编号数据
    getVacancySentryIdData() {
      const result = [];
      getDelectSentry("/data/unlessSerial")
        .then(res => {
          console.log(res);
          if (res.data.code == "1") {
            let resData = res.data.data;
            console.log(res.data.data);
            for (let i = 0; i < resData.length; i++) {
              result.push[(Array(5).join(0) + resData[i]).slice(-5)];
            }
            console.log(result);
            this.VacancySentryIdList = resData;
          } else if (res.data.code == "0") {
            this.$message({
              message: res.data.data,
              type: "warning"
            });
          } else if (res.data.code == "-1") {
            this.$message({
              message: "网络错误!",
              type: "error"
            });
          }
        })
        .catch(err => {
          console.log(err);
          this.$message({
            message: "网络错误",
            type: "error"
          });
        });
    },

    getReportEventTypeData() {
      console.log('搜索关键字：' + JSON.stringify(this.form))
      let formData = this.form
      let code = ''
      if (
        formData.unitTown == '' &&
        formData.postLevel == '' &&
        formData.postCode == ''
      ) {
        code = ''
      } else if (
        formData.unitTown != '' &&
        formData.postLevel == '' &&
        formData.postCode == ''
      ) {
        code = formData.unitTown + '-'
      } else if (
        formData.unitTown != '' &&
        formData.postLevel != '' &&
        formData.postCode == ''
      ) {
        code = formData.unitTown + '-' + formData.postLevel
      } else if (
        formData.unitTown != '' &&
        formData.postLevel != '' &&
        formData.postCode != ''
      ) {
        code = formData.postCode
      }

      let name = ''
      let eventName = ''
      let time = formData.reportTime
      let startTime = formData.reportStartTime
      console.log(parseInt(formData.reportEndTime.split("-")[2])+1)
      let arrayTime = new Array;
      arrayTime[0] = formData.reportEndTime.split("-")[0]
      arrayTime[1] = formData.reportEndTime.split("-")[1]
      arrayTime[2] = (parseInt(formData.reportEndTime.split("-")[2])+1)<10?("0"+(parseInt(formData.reportEndTime.split("-")[2])+1)):(parseInt(formData.reportEndTime.split("-")[2])+1)
      let endTime = arrayTime.join("-")
      if(time == null) time = ""
      if(startTime == null) time = ""
      if(endTime == null) time = ""
      const result = []
      getReportEventType(
       '/report/eventCount?postCode=' +
          code +
          '&person=' +
          name +
          '&eventName=' +
          eventName +
          '&startTime=' +
          startTime +
          '&endTime=' +
          endTime
      )
        .then(res => {
          if (res.data.code == "1") {
            let resData = res.data.data;
            console.log(res.data.data);
            // this.drawLine(res.data.data)
          } else if (res.data.code == "0") {
            this.$message({
              message: res.data.data,
              type: "warning"
            });
          } else if (res.data.code == "-1") {
            this.$message({
              message: "网络错误!",
              type: "error"
            });
          }
        })
        .catch(err => {
          console.log(err);
          this.$message({
            message: "网络错误",
            type: "error"
          });
        });
    },

    // drawLine(data){
    //     // 基于准备好的dom，初始化echarts实例
    //     let myChart = this.$echarts.init(document.getElementById('myChart'))
    //     // 绘制图表
    //     myChart.setOption({
    //         title: { text: '在Vue中使用echarts' },
    //         tooltip: {},
    //         xAxis: {
    //             data: data.name
    //         },
    //         yAxis: {},
    //         series: [{
    //             name: '销量',
    //             type: 'bar',
    //             data: data.count
    //         }]
    //     });
    // },

    // 编辑哨兵
    editSentryInfo(index, row) {
      console.log(index, row);
      this.modelName = "编辑哨兵";
      this.isShowUserUpload = false;
      this.addUserDataModel = true;
      this.isAddType = false;
      this.editUserId = row.id;
      this.addOrEditUserData.userName = row.userName;
      this.addOrEditUserData.password = row.password;
      this.addOrEditUserData.grade = row.grade;
      this.addOrEditUserData.nickName = row.nickName;
      this.addOrEditUserData.headImage = row.headImage;
    },

    // 提交---哨兵的编辑和提交
    SubmitUserData() {
      if (this.isAddType) {
        let data = {
          userName: this.addOrEditUserData.userName,
          password: this.addOrEditUserData.password,
          grade: this.addOrEditUserData.grade,
          nickName: this.addOrEditUserData.nickName,
          headImage: this.addOrEditUserData.headImage
        };
        console.log(data);
        console.log("add");
        addUsers("/admin/add", data)
          .then(res => {
            console.log(res);
            if (res.data.code == "1") {
              this.$message({
                type: "success",
                message: "添加用户信息成功!"
              });
              this.addOrEditUserData = JSON.parse(
                JSON.stringify(this.addOrEditUserDataEmpty)
              );
              this.addUserDataModel = false;
              this.isShowUserUpload = true;
              // 重新更新列表数据
              this.getTableData();
            } else if (res.data.code == "0") {
              this.$message({
                message: res.data.data,
                type: "warning"
              });
            } else if (res.data.code == "-1") {
              this.$message({
                message: "网络错误!",
                type: "error"
              });
            }
          })
          .catch(err => {
            this.$message({
              message: "网络错误!",
              type: "error"
            });
          });
      } else {
        let data = {
          id: this.editUserId,
          password: this.addOrEditUserData.password,
          grade: this.addOrEditUserData.grade,
          nickName: this.addOrEditUserData.nickName
        };
        console.log(data);
        console.log("upload");
        uploadUsers("/admin/update", data)
          .then(res => {
            console.log(res);
            if (res.data.code == "1") {
              this.$message({
                type: "success",
                message: "修改哨兵信息成功!"
              });
              this.addOrEditUserData = JSON.parse(
                JSON.stringify(this.addOrEditUserDataEmpty)
              );
              this.addUserDataModel = false;
              this.isShowUserUpload = true;
              // 重新更新列表数据
              this.getTableData();
            } else if (res.data.code == "0") {
              this.$message({
                message: res.data.data,
                type: "warning"
              });
            } else if (res.data.code == "-1") {
              this.$message({
                message: "网络错误!",
                type: "error"
              });
            }
          })
          .catch(err => {
            this.$message({
              message: "网络错误!",
              type: "error"
            });
          });
      }
    },

    // 删除哨兵提示框
    deleteUserInfo(index, row) {
      console.log(index, row);
      let delectId = row.id;
      console.log("delectId:" + delectId);
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.deleteUserInfoSure(delectId);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },

    // 删除哨兵数据
    deleteUserInfoSure(delectId) {
      console.log("delete");
      console.log(delectId);
      delectUsers("/admin/delete?id=" + delectId)
        .then(res => {
          console.log(res);
          if (res.data.code == "1") {
            this.$message({
              type: "success",
              message: "删除成功!"
            });
            // 重新更新列表数据
            this.getTableData();
          } else if (res.data.code == "0") {
            this.$message({
              message: res.data.data,
              type: "warning"
            });
          } else if (res.data.code == "-1") {
            this.$message({
              message: "网络错误!",
              type: "error"
            });
          }
        })
        .catch(err => {
          this.$message({
            message: "网络错误!",
            type: "error"
          });
        });
    }
  }
};
</script>


<style lang='less'>
@import "../publicResources/common";
</style>


