import login from '@/view/login'
import postlist from '@/view/post-list'
import reporteventlist from '@/view/report-event-list'
import sentrylist from '@/view/sentry-list'
import Integrallist from '@/view/integral-list'
import Integralrank from '@/view/integral-rank'
import userManage from '@/view/user-manage'

let routes = [{
    path: '/',
    name: '登录',
    component: login
  },
  {
    path: '/index',
    name: '登录',
    component: login
  },
  {
    path: '/Integrallist',
    name: '积分列表',
    component: Integrallist
  },
  {
    path: '/postlist',
    name: '哨所模块',
    component: postlist
  },
  {
    path: '/reporteventlist',
    name: '事件模块',
    component: reporteventlist
  },
  {
    path: '/sentrylist',
    name: '哨兵模块',
    component: sentrylist
  },
  {
    path: '/Integralrank',
    name: '积分排行',
    component: Integralrank
  },
  {
    path: '/userManage',
    name: '用户管理',
    component: userManage
  }
]
export default routes;



