import {
    fetch
} from "./fetch"; //引用fetch.js
import api from './url'; //引用url.js
const hostUrl = 'http://116.62.138.218:8889'; //(上线地址)
// const hostUrl = 'http://192.168.31.222:8889'; //（本地地址）


// 有多少接口类型都写在后面，其他地方直接调用

// *****************************************************************************************登录部分

// 账号登录接口
export function login(isUrl, dataJson) {
    console.log(dataJson)
    return fetch({
        async: true,
        url: hostUrl + isUrl,
        method: 'post',
        headers: {
            "content-type": "application/json",
            "cache-control": "no-cache"
        },
        data: dataJson,
    })
}


