<template>
  <div class="header">
    <el-row type="flex" justify="center">
      <el-col :span="24">
        <div class="headerCon">
          <ul class="overview">
            <li>哨兵总数:
              <span v-text="sentryNum"></span>
            </li>
            <li>哨所总数:
              <span v-text="postNum">1121</span>
            </li>
            <li>事件总数:
              <span v-text="eventNum">1121</span>
            </li>
          </ul>
          <ul class="user">
            <li class="signOut" @click="clickNavDown"><img src="../assets/annet_nav_down.png" alt=""></li>
            <li class="userInfo">
              <span>{{userName}}</span><img src="../assets/btn_homepage_patients.png" alt="">
            </li>
          </ul>
          <span class="exit" v-show="ifShow" @click="openExitSystem">退出账号</span>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import {
  getSentryList,
  getPostList,
  getReportEventList
} from '../axios/api'
export default {
  data() {
    return {
      ifShow: false,
      userName: 'admin',
      sentryNum: 0,
      postNum: 0,
      eventNum: 0
    }
  },
  created() {
    this.getSentryPostEventNum()
    console.log(JSON.parse(sessionStorage.getItem('user')).nickName)
    let nickName = JSON.parse(sessionStorage.getItem('user')).nickName
    this.userName = nickName
  },
  methods: {
    clickNavDown() {
      this.ifShow === true ? (this.ifShow = false) : (this.ifShow = true)
    },
    openExitSystem() {
      this.$router.push({ path: '/' })
    },
    // 获取哨兵、哨所、事件总数
    getSentryPostEventNum() {
      let array = [
        '/report/page?page=0&size=10&postCode=&person=&eventName=&time=',
        '/post/page?page=0&size=10&code=&grade=',
        '/sentry/page?page=0&size=10&idNumber=&name=&postCode='
      ]
      for (let i in array) {
        if (i==2) {
          getSentryList(array[2])
            .then(res => {
              if (res.data.code == '1') {
                let resData = res.data.data.totalElements
                console.log(resData)
                this.sentryNum = resData
              } else if (res.data.code == '0') {
                this.$message({
                  message: res.data.data,
                  type: 'warning'
                })
              } else if (res.data.code == '-1') {
                this.$message({
                  message: '网络错误!',
                  type: 'error'
                })
              }
            })
            .catch(err => {
              console.log(err)
              this.$message({
                message: '网络错误',
                type: 'error'
              })
            })
        }else if(i==1) {
          getPostList(array[1])
            .then(res => {
              if (res.data.code == '1') {
                let resData = res.data.data.totalElements
                console.log(resData)
                this.postNum = resData
              } else if (res.data.code == '0') {
                this.$message({
                  message: res.data.data,
                  type: 'warning'
                })
              } else if (res.data.code == '-1') {
                this.$message({
                  message: '网络错误!',
                  type: 'error'
                })
              }
            })
            .catch(err => {
              console.log(err)
              this.$message({
                message: '网络错误',
                type: 'error'
              })
            })
        }else if(i==0) {
          getReportEventList(array[0])
            .then(res => {
              if (res.data.code == '1') {
                let resData = res.data.data.totalElements
                console.log(resData)
                this.eventNum = resData
              } else if (res.data.code == '0') {
                this.$message({
                  message: res.data.data,
                  type: 'warning'
                })
              } else if (res.data.code == '-1') {
                this.$message({
                  message: '网络错误!',
                  type: 'error'
                })
              }
            })
            .catch(err => {
              console.log(err)
              this.$message({
                message: '网络错误',
                type: 'error'
              })
            })
        }
      }
    }
  }
}
</script>


<style lang="less">
.header {
  height: 40px;
  width: 100%;
  background: #fff;
  div.headerCon {
    float: right;
    width: 100%;
    height: 40px;
    ul.overview {
      line-height: 40px;
      margin: 0;
      padding: 0;
      text-align: center;
      width: 50%;
      height: 40px;
      float: left;
      li {
        list-style: none;
        display: block;
        height: 100%;
        line-height: 40px;
        float: left;
        text-align: center;
        margin-right: 30px;
        font-size: 18px;
      }
    }
    ul.user {
      line-height: 40px;
      margin: 0;
      padding: 0;
      width: 50%;
      padding-right: 20px;
      float: right;
      li {
        list-style: none;
        display: block;
        height: 100%;
        line-height: 40px;
        float: right;
        padding-left: 6px;
      }
      .userInfo {
        img {
          width: 1.4rem;
          margin-top: 8px;
          float: right;
        }
        span {
          font-size: 14px;
          padding-right: 10px;
        }
      }
      .signOut {
        img {
          width: 80%;
          margin-top: 10px;
          cursor: pointer;
        }
      }
    }
    .exit {
      box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.3);
      border-radius: 4px;
      display: block;
      position: absolute;
      top: 35px;
      right: 20px;
      background: #ffffff url('../assets/shutDown.png') no-repeat center left;
      background-size: 16%;
      background-position-x: 6px;
      padding: 6px 6px 6px 24px;
      font-size: 12px;
      cursor: pointer;
    }
  }
}
</style>



